<template>
  <div class="readme-container">
        <div class="notice">  
          <h3 class="title"></h3>
          <marquee behavior="" direction="">
                             
          </marquee> 
        </div>
        <!-- <router-link to="./echarts"><img src="../.././assets/sale-more.jpg"><span></span></router-link> -->
        <ul class="headData">
          <router-link to="/index/user/echarts">
            <li class="card count">
              <div class="order">
                <p class="todayOrd"></p>
                <p class="num">11320</p>
              </div>
              <div class="icon">
                <i class="fa fa-line-chart" style="font-size: 19px;"></i>
              </div>
            </li>
          </router-link>

          <router-link to="/index/order/index">
            <li class="card money">
              <div class="order">
                <p class="todayMoney"></p>
                <p class="num">10690402</p>
              </div>
              <div class="icon">
                <i class="fa fa-usd" style="font-size: 19px;"></i>
              </div>
            </li>
          </router-link>

          <router-link to="/index/user/echarts">
            <li class="card count">
              <div class="order">
                <p></p>
                <p class="num">11220</p>
              </div>
              <div class="icon">
                <i class="fa fa-line-chart" style="font-size: 19px;"></i>
              </div>
            </li>
          </router-link>
          
          <router-link to="/index/order/index">
            <li class="card money">
              <div class="order">
                <p></p>
                <p class="num">10630408</p>
              </div>
              <div class="icon">
                <i class="fa fa-usd" style="font-size: 19px;"></i>
              </div>
            </li>
          </router-link>

        </ul>
        <!--  -->
        <div id="chartLine" style="width:800px; height:500px;"></div>
  </div>
</template>

<script>
  import echarts from 'echarts'

  export default {

    data() {
      return {
        chartLine: null,
      }
    },
    methods:{

      drawLineChart() {
          this.chartLine = echarts.init(document.getElementById('chartLine'));
          this.chartLine.setOption({
              title: {
                  text: '',
                  left:"1%"
              },
              tooltip: {
                  trigger: 'axis'

              },
              legend: {
                  data: ['', '', ''],
                  right:"3%"
              },
              grid: {
                  left: '10%',
                  right: '4%',
                  bottom: '3%',
                  containLabel: true
              },
              xAxis: {
                  type: 'category',
                  boundaryGap: false,
                  data: ['', '', '', '', '', '', '']
              },
              yAxis: {
                  type: 'value'
              },
              series: [
                  {
                      name: '',
                      type: 'line',
                      stack: '',
                      data: [120, 132, 101, 134, 90, 230, 210]
                  },
                  {
                      name: '',
                      type: 'line',
                      stack: '',
                      data: [220, 182, 191, 234, 290, 330, 310]
                  },
                  {
                      name: '',
                      type: 'line',
                      stack: '',
                      data: [820, 932, 901, 934, 1290, 1330, 1320]
                  }
              ]
          });
      },
      drawCharts() {
        this.drawLineChart()
      },
    },
    mounted: function () {
            this.drawCharts()
        },
    updated: function () {
            this.drawCharts()
        }
  };
</script> 


<style type="text/css">
.readme-container{

}
/**/
.notice {
  margin-left: 20px;
  width: 96%;
  overflow: hidden;
  background: #FCFCFC;
  padding: 10px;
}
.readme-container .title {
  padding-bottom: 10px;
  color: red;
}
/**/
ul.headData{
  position: relative;
  overflow: hidden;
  margin-top: 30px;
  margin-left: 120px;
}
ul a{
  font-size: 18px;
}
/**/
li.card{
  float: left;
  margin-left: 30px;
  list-style: none;
  border: 1px solid #e1e1e1;
  width: 200px;
  height: 60px;
  color: white;
}
.card.count{
  background-color:#95CACA;
}
.card.money{
  background-color:#CDCD9A;
}
/**/
.order{
  float: left;
  margin-left: 30px;
  margin-top: 10px;
}

p.todayOrd{

}
p.num{
  text-align: center;
}
/**/
.icon{
  float: right;
}
.icon i{
  font-size: 29px;
  margin-top: 16px;
  margin-right: 20px;
}
#chartLine{
  margin: 100px 150px;

}
</style>



