<template>
  <div class="power">
     <!--  -->
     <!-- <div class="bre-crumb">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>  
        </el-breadcrumb>
     </div> -->

     <!--  -->
     <div class="newRole">
        <el-button type="primary" icon="edit" @click="handleCreate"></el-button>
     </div>

     <!--  -->
     <div class="info-con"> 
       <el-table ref="multipleTable" :data="tableData1"  border tooltip-effect="dark" style="width: 100%"
        @selection-change="handleSelectionChange">
           <el-table-column type="selection" width="55"></el-table-column>
           <el-table-column algin="center" label="" width="120">
              <template scope="scope">
                 <template >
                      <span >{{ scope.$index  }} &nbsp; &nbsp;</span>
                 </template > 
              </template>
           </el-table-column>
           <el-table-column prop="roleName" label="" width="120" ></el-table-column>
           <el-table-column label="" width="300">
              <template scope="scope">
                 <template v-for="item in scope.row.userList">
                      <span >{{ item.userName  }} &nbsp; &nbsp;</span>
                 </template > 
              </template>
           </el-table-column>
         
           <el-table-column label="" width="400">
             <template scope="scope">
               <el-button v-waves icon="edit" size="small" @click="setPermissions(scope.$index, scope.row)" ></el-button>
               <el-button v-waves icon="edit" size="small" @click="setUser(scope.$index, scope.row)"></el-button>
               <el-button v-waves icon="edit" size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
               <el-button v-waves icon="delete" size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
             </template>
           </el-table-column>
       </el-table>
       <div style="margin-top: 20px">
          <el-button v-waves @click="toggleSelection([tableData1[0], tableData1[1],tableData1[2],tableData1[3],tableData1[4],tableData1[5],tableData1[6]])"></el-button>
          <el-button v-waves @click="toggleSelection()"></el-button>
       </div>
     </div>

     <!--  -->
     <div class="add">
        <!-- <el-button type="text" @click="dialogFormVisible = true"> Dialog</el-button> -->

        <el-dialog title="" :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.roleName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.remark" auto-complete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>
            <el-button type="primary"  @click="handleCreateSubmit"> </el-button>
          </div>
        </el-dialog>
     </div>

     <!--  -->
     <div class="setPermission">
        <el-dialog title="" :visible.sync="dialogPermissionsVisible">
              <el-form class="small-space" :model="form" label-position="left" label-width="70px" style='width: 100%; '>
               <!-- <el-checkbox-group v-model="smMenuBeanDtoList"> -->
                <template v-for="item in permissionMenuList">
                    <el-checkbox  label="item.url" name="type" style="margin:0 15px 15px 0;"  v-model="item.checked">{{ item.menuName }}</el-checkbox> 
                </template>
                  
                <!-- </el-checkbox-group> -->
              </el-form>

              <div slot="footer" class="dialog-footer">
                <el-button @click="dialogPermissionsVisible = false"> </el-button>
                <el-button type="primary" @click="setPermissionsSubmit"> </el-button>
              </div>
        </el-dialog>
     </div>

      <!--  -->
     <div class="info-footer">
        <div class="block">
           <span class="demonstration"></span>
           <el-pagination
             @size-change="handleSizeChange"
             @current-change="handleCurrentChange"
             :current-page="currentPage4"
             :page-sizes="[25, 50, 75, 100]"
             :page-size="25"
             layout="total, sizes, prev, pager, next, jumper"
             :total="100">
           </el-pagination>
         </div>

    </div>

  </div>
</template>
<script>
  import {api} from '@/global/api'
  export default {
      data () {
          return {
              // 
              tableData1: null,
              multipleSelection: [],//
              //
              dialogFormVisible: false,
              //
              permissionMenuList:[
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true}
              ],
              //
              dialogPermissionsVisible: false,
              form: {
                     roleName: '',
                     remark: ''
                     },
              formLabelWidth: '120px',
              // 
              currentPage4: 4
            
          }
      },
      mounted:function(){
         var vm=this;
         vm.getData();//JSON
      },
      methods:{

        //json
         getData(){
          // return this.bb;

          this.$http.get(api.power).then(function(response){
              // alert(1);
            console.log('response',response)
            // console.log("json",response.data.tableData1)
            this.tableData1 = response.data.tableData1;

          }, function(response){
            alert("")
          })
         },

        //form
        initForm(){
           let vm = this;
           vm.form = {
                    roleName: '',
                    remark: ''   
           }
        },
        //
        handleCreate(){
          this.initForm();
          console.log("");
          this.dialogFormVisible = true;
        },
        //
        handleCreateSubmit(){
          let vm = this;
          console.log('',vm.form);
          vm.tableData1.push(vm.form);
          console.log('',vm.tableData1);
          this.dialogFormVisible = false;

        },
        //
        initpermissionMenuList(){
          let vm = this;
          vm. permissionMenuList = [
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true},
                  {menuName:"",checked:true}
              ]
        },
        //
        setPermissions(){
          console.log("");
          this.initpermissionMenuList();

          this.dialogPermissionsVisible = true;
        },
        //
        setPermissionsSubmit(){
          console.log("");
          this.$message({
            message:"",
            type:'success'

          });
          this.dialogPermissionsVisible = false;//
        },
        //
        setUser(){
          console.log("");
          this.$message({
            message:"",
            type:'warning'

          });

        },
        //
        handleEdit(index, row) {
           console.log(index, row);
        },
        //
        handleDelete(index, row) {
           console.log("index",index);
           console.log("row",row);
            // console.log(index, row);
           this.tableData1.splice(index,1);

        },
        //
        toggleSelection(rows) {
          if (rows) {
            rows.forEach(row => {
              this.$refs.multipleTable.toggleRowSelection(row);
            });
          } else {
            this.$refs.multipleTable.clearSelection();
          }
        },
        //
        handleSelectionChange(val) {
          this.multipleSelection = val;
        },
        //
         handleSizeChange(val) {
          console.log(` ${val} `);
         },
        handleCurrentChange(val) {
          console.log(`: ${val}`);
         },


       
      }
   }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  .power{
    position: absolute;
  }
  .newRole{
    margin: 40px 20px;
  }
  .info-con{
    margin: 20px 10px;
    text-align: left;
  }
  .info-footer{
    margin-top: 50px;
  }
 
</style>


