<template>
  <div class="customerInfo">
<!--  -->

<div class="cum_search">
     <el-select v-model="value" placeholder="">
       <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value">
      </el-option>
    </el-select>

  <div class="ads">
  <el-input v-model="input" placeholder="" ></el-input>
  <!-- `checked`  true  false -->
  <el-checkbox v-model="checked"></el-checkbox>

  <span class="wrapper">
    <el-button type="success"></el-button>
  </span>
  </div>  
</div>

<!--   -->
  <div class="add_btn">
       <el-button type="primary" @click="handleCreate"></el-button>
         <el-dialog title="" :visible.sync="dialogFormVisible">             
              <el-form :model="form">
                <el-form-item label="" width="formLabelWidth">
                <el-select v-model="form.type" placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option label="" value=""></el-option>
                </el-select>
                </el-form-item>

                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.level" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.id" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.title" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.staff" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.name" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.phone" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="QQ//Email" :label-width="formLabelWidth">
                  <el-input v-model="form.chat" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.money" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.address" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.inventory" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                  <el-input v-model="form.state" auto-complete="off"></el-input>
                </el-form-item>
                
              </el-form>
         <div slot="footer" class="dialog-footer">

        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="handleCreateSubmit()"> </el-button> 
      </div>
    </el-dialog>

       <el-button disabled="disabled"></el-button>
       <el-button disabled="disabled"></el-button>
       <el-button @click="handleDelete" disabled="disabled"></el-button>    
  </div>
<!--  -->
 <div class="cum_table">
    <el-table
    ref="multipleTable"
    :data="tableData3"
    border
    tooltip-effect="dark"
    style="width: 100%"
    @selection-change="handleSelectionChange">
    <el-table-column
      type="selection"
      width="40" align="center">
    </el-table-column>
      <el-table-column
        prop="type"
        label=""
        width="95" 
        show-overflow-tooltip
        align="center">
      </el-table-column>
      <el-table-column
        prop="level"
        label=""
        width="95" 
        show-overflow-tooltip 
        align="center">
      </el-table-column>
      <el-table-column
        prop="id"
        label=""
        width="100"
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="title"
        label=""
        width="100" show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="staff"
        label=""
        width="100" 
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="name"
        label=""
        width="95" 
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="phone"
        label=""
        width="100" 
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="chat"
        label="QQ/Email"
        width="120" 
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="money"
        label=""
        width="100" 
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="address"
        label=""
        width="100" 
        align="center"
        show-overflow-tooltip>
      </el-table-column>
      <el-table-column
        prop="inventory"
        label=""
        width="100">
      </el-table-column>
      <el-table-column
        prop="state"
        label=""
        width="80" >        
      </el-table-column>
    </el-table>

     <div class="tab_btn" style="margin-top: 20px">
      <el-button @click="toggleSelection([tableData3[0],tableData3[1],tableData3[2]])"></el-button>
      <el-button @click="toggleSelection()"></el-button>
    </div>
    
    <div class="left_btn">
       <el-select v-model="value2" placeholder="">
      <el-option
        v-for="item in options2"
        :key="item.value"
        :label="item.label"
        :value="item.value"
        :disabled="item.disabled">
      </el-option>
  </el-select>
      <el-button></el-button>
      <el-button></el-button>
    </div>
    <div class="right_btn">
      <el-button></el-button>
      <el-button></el-button>
    </div> 
    </div>   


  </div>
</template>


<script>
  export default {
      data () {
          return {
           options: [{
          value: '1',
          label: ''
          }, {
          value: '2',
          label: 'VIP'
        }],
          value: '',
          input: '',
          checked: false,
           tableData3: [{
              type: '',
              level: '',
              id: '21062564789',
              title: '',
              staff: '',
              name: '',
              phone: '16598742351',
              chat: '1032658897',
              money: '12222.00',
              address: '',
              inventory: '2365',
              state:''             
          },{
              type: '',
              level: 'VIP',
              id: '244452564789',
              title: '',
              staff: '',
              name: '',
              phone: '18956562351',
              chat: '4278538897',
              money: '55555.00',
              address: '',
              inventory: '3120',
              state:''             
          },{
              type: '',
              level: 'VIP',
              id: '555552564789',
              title: '',
              staff: '',
              name: '',
              phone: '13456562351',
              chat: '4678533333',
              money: '44444.00',
              address: '',
              inventory: '1120',
              state:''             
          }],
          multipleSelection: [],
              options2: [{
              value: '1',
              label: ''
            }, {
              value: '2',
              label: '',
              disabled: true
            }, {
              value: '3',
              label: ''
            }, {
              value: '4',
              label: ''
            }],
            value2: '',
            dialogFormVisible: false,
            form: {             
              type: '',
              level: '',
              id: '',
              title: '',
              staff: '',
              name: '',
              phone: '',
              chat: '',
              money: '',
              address: '',
              inventory: '',
              state:'',
              delivery: false,            
            },
            formLabelWidth: '100px'          
          }
        },
         methods: {
            toggleSelection(rows) {
              if (rows) {
                rows.forEach(row => {
                  this.$refs.multipleTable.toggleRowSelection(row);
                });
              } else {
                this.$refs.multipleTable.clearSelection();
              }
            },
            handleSelectionChange(val) {
              this.multipleSelection = val;
            },
            //
            initForm(){
              let vm = this;
              vm.form = {
                type: '',
                level: '',
                id: '',
                title: '',
                staff: '',
                name: '',
                phone: '',
                chat: '',
                money: '',
                address: '',
                inventory: '',
                state:'',
                delivery: false,        

              }
            },
            // 
            handleCreate(){
            //
              this.initForm();
              this.dialogFormVisible = true;
            },
            // 
            handleCreateSubmit(){
              var vm = this ;
              console.log(':',vm.form);
              vm.tableData3.push(vm.form);
              console.log(':',vm.tableData3);
              this.dialogFormVisible = false;
              // 
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              });
            },
            // 
            handleDelete(row,index){
              var vm = this ;
              console.log("row",vm.multipleSelection);
              // splice
              this.tableData3.splice(vm.multipleSelection,vm.multipleSelection.length);
              console.log("",vm.multipleSelection.length);
              this.tableData3.splice(index,1);
              
            }



          }
       
   }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  a {
    text-decoration: none;
  }
  /**/
 .customerInfo{
    position: absolute;
    /*background:#FEF5E7;*/
    width: 1176px;
  }
  .customerInfo .el-breadcrumb{
    height: 58px;
    line-height: 58px;
  }
  /**/
  .customerInfo .cum_search{
    margin-left: 20px;
  }
  .customerInfo .wrapper span{
    color:#fff;
  }
  .customerInfo .ads
  {
    display: inline;   
  }
  .customerInfo .ads .el-input{
    width: 22%; 
    margin-right: 26px 
  }
 .customerInfo .el-checkbox__input.is-checked .el-checkbox__inner {
    background-color: #ccc; 
    border-color: #ccc;
  }
  .customerInfo .ads .el-button--success {
    background: #75bd5a;
    border: 1px solid #85ae74;
  }
  .customerInfo .el-select-dropdown__item.selected {
    background-color: #ccc;

  }
  .customerInfo .el-select-dropdown__list {
    padding:0; 
  }
  /**/
  .customerInfo .cum_table .el-button{
    background: #dfe6ec;
  }
  .customerInfo .add_btn{
    float: right;
    margin-bottom: 10px;
  }
  .customerInfo .tab_btn .el-button{
    background: #fff;
    margin-left: 20px;
  }
  .customerInfo .left_btn{
    float: left;
    margin-top: 10px;
    margin-left: 20px;
  }
  .customerInfo .right_btn{
    float: right;
    margin-top: 10px;
  }
</style>


