<template>
  <div class="post1">
    <!-- 
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb> -->

    <div class="one">
      <el-select v-model="value9" multiple filterable remote placeholder="detail" :remote-method="remoteMethod" :loading="loading">
        <el-option v-for="item in options4" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select>

      <el-button  type="info"><i class="fa fa-search" @click="handleSearch" aria-hidden="true"></i>  </el-button>
      <el-button  type="success" @click="handleCreate"><i class="fa fa-plus-circle" aria-hidden="true"></i>  </el-button>
      <el-button  type="success"><i class="fa fa-download" aria-hidden="true" @click="handleDownload"></i>  </el-button>
    </div>
    
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">

          <el-form class="small-space" :model="temp" label-position="left" label-width="70px" style='width: 400px; margin-left:50px;'>
            <el-form-item label="">
              <el-input v-model="temp.name"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="temp.address"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="temp.detail"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="temp.manager"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="temp.start"></el-input>
            </el-form-item> 
            <el-form-item label="">
              <el-input v-model="temp.price"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="temp.amount"></el-input>
            </el-form-item>            
          </el-form>

          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false"> </el-button>           
            <el-button type="primary" @click="handleCreateSubmit"> </el-button>
          </div>
    </el-dialog>
    
    <!--  -->
    <div class="two">
    <el-table ref="multipleTable" :data="tableData3" border tooltip-effect="dark" style="width: 100%" @selection-change="" height="500" show-summary>

        <el-table-column type="selection" width="55">
        </el-table-column>

        <!-- <el-table-column label="ID" width="120">
          <template scope="scope">{{ scope.row.date }}</template>
        </el-table-column> -->
        
        <el-table-column prop="ID" label="ID" width="80" align="center">
            <template scope="scope">
              {{scope.$index + 1 }}
            </template>
        </el-table-column>


        <el-table-column prop="name" label="" width="100" align="center">
        </el-table-column>

        <el-table-column prop="address" label="" show-overflow-tooltip width="250" align="center">
        </el-table-column>

        <el-table-column prop="detail" label="" show-overflow-tooltip width="100" align="center">
        </el-table-column>

        <el-table-column prop="manager" label="" show-overflow-tooltip align="center">
        </el-table-column>

        <el-table-column prop="start" label="" show-overflow-tooltip width="100" align="center">
        </el-table-column>
        
        <el-table-column prop="price" label="" sortable show-overflow-tooltip width="100" align="center">
        </el-table-column>

        <el-table-column prop="amount" label="" sortable show-overflow-tooltip width="100" align="center">
        </el-table-column>

        <el-table-column prop="work" label="" show-overflow-tooltip width="150">
            <template scope="scope">
                    <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>

                    <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">
                        
                    </el-button>

            </template>
        </el-table-column>

    </el-table>
    </div>
    
    <!--  -->
    <div class="block">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="currentPage4" :page-sizes="[100, 200, 300, 400]" :page-size="100" layout="total, sizes, prev, pager, next, jumper" :total="400">
        </el-pagination>
    </div>

  </div>
</template>
<script>
  import {api} from '@/global/api'
  import {export_json_to_excel} from '@/vendor/Export2Excel';//

  export default {
      data () {
          return {
            tableData3: [                   
            ],
            //
            options4: [],
            value9: [],
            list: [],
            loading: false,
            states: [],
            temp: {
                "name": "",
                "address": "",
                "detail":"",
                "manager":"",
                "start":"",
                "price":"",
                "amount":"",
            },
            dialogFormVisible: false,
            currentPage1: 5,
            currentPage2: 5,
            currentPage3: 5,
            currentPage4: 4
          }
        },
        mounted() {
             var vm = this;
             vm.getList();
           },

      methods: {
            getList: function() {
                var vm = this;
                this.$http.get(api.chakan).then(function(response) {
                    var data = response.body;
                    vm.tableData3 = data.data;

                    //json
                    vm.list = data.data;
                    for(var day of vm.list){
                       vm.states.push(day.detail);
                    }   
                    vm.list = vm.states.map(item => {
                      return { value: item, label: item };
                    });
                    }, function(response) {
                      alert("");
                })
            },
            remoteMethod(query) {
              if (query !== '') {
                this.loading = true;
                setTimeout(() => {
                  this.loading = false;
                  this.options4 = this.list.filter(item => {
                    return item.label.toLowerCase()
                      .indexOf(query.toLowerCase()) > -1;
                  });
                }, 200);
              } else {
                this.options4 = [];
              }
            },
            handleEdit(index, row) {
              console.log(index, row);
            },

            //  
            handleSearch() {
              // console.log('submit!');
              var vm = this;
              // 
              this.$http.get(api.chakan,{params:vm.name}).then(function(response){
                  // alert(1);
                console.log('response',response)
                // console.log("json",response.data.tableData3)
                this.tableData3 = response.data.tableData3;

              }, function(response){
                alert("")
              })

            },


            //
            handleDelete(index,row){
                var vm = this;
                console.log('row',index,'-----',row);
                //
                vm.tableData3.splice(index,1)
            },
            //
            handleDelAll(){
                var vm = this;
                console.log('row',vm.multipleSelection)
            },
            //
            handleCreate() {
                this.temp = {
                "name": "",
                "address": "",
                "detail":"",
                "manager":"",
                "start":"",
                "price":"",
                "amount":"",
            };
                this.dialogFormVisible = true;
            },
            //
            handleCreateSubmit(){
                var vm = this;
                console.log('',vm.temp,vm.tableData3)

                // 
                vm.tableData3.push(vm.temp)
                console.log('',vm.tableData3)
                
                this.dialogFormVisible = false;
            },
            handleSelectionChange(val) {
                this.multipleSelection = val;
            },
            //
            handleDownload() {
              var vm = this;
              require.ensure([], () => {
                // const { export_json_to_excel } = require('../../vendor/Export2Excel');
                const tHeader = ['', '', '', '', '','',''];
                const filterVal = ['name', 'address', 'detail', 'manager', 'start','price','amount'];
                const list = vm.tableData3;
                const data = vm.formatJson(filterVal, list);
                export_json_to_excel(tHeader, data, 'excel');
              })
            },
            //
            handleSizeChange(val) {
                console.log(` ${val} `);
            },
            handleCurrentChange(val) {
                console.log(`: ${val}`);
            }
          }
   }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  a {
    text-decoration: none;
  }
  .post1{
    margin:20px;
    position: absolute;
  }
  .post1 .one{
    margin-bottom: 10px;
  }
  .post1 .block{
    margin-top: 20px;
  }
  .post1 .dialog-footer{
    clear: both;
  }
</style>


