<!---->
<template>
 <div class="createPost-container" v-cloak>
    <div class="createPost-main-container" v-loading="infoLoading">
      <div class="filter-container">
        <el-row :gutter="10">
          <el-col :span="4">
            <span style="font-size: 25px;color: #f96b6b;"></span>
          </el-col>
          <el-col :span="12" style="padding-top:10px;">
            <span style="font-size: 14px;color: #98a8b8;">{{ orderId }} &nbsp;&nbsp;&nbsp;&nbsp; DH-O-20170721-095606</span>
          </el-col>
          <el-col :span="8">
            <!--popover-->
            <el-popover
              ref="sendGoodsPopover"
              placement="top-start"
              width="1000"
              trigger="click"
              v-model="showSendGoodsPopover">
              <el-form label-width="80px" label-position="right" :model="sendGoodsInfo" :rules="rules">
                <el-row>
                  <el-col :span="8">
                    <el-row>
                      <el-col>
                        <el-form-item label="" prop="sendTime">
                          <el-date-picker
                            type="datetime"
                            placeholder=""
                            clearable
                            size="small" v-model="sendGoodsInfo.sendTime">
                          </el-date-picker>
                        </el-form-item>
                      </el-col>
                      <el-col>
                        <el-form-item label="" prop="logisticsCompany">
                          <el-select v-model="sendGoodsInfo.logisticsCompany" clearable placeholder="" size="small">
                            <el-option
                              v-for="item in selectLogisticsCompany"
                              :key="item.key"
                              :label="item.label"
                              :value="item.key">
                            </el-option>
                          </el-select>
                        </el-form-item>
                      </el-col>
                      <el-col>
                        <el-form-item label="" prop="logisticsNum">
                          <el-input
                            size="small"
                            placeholder="" v-model="sendGoodsInfo.logisticsNum"></el-input>
                        </el-form-item>
                      </el-col>
                    </el-row>
                  </el-col>
                  <el-col :span="12">
                    <el-form-item label="" prop="logisticsTips">
                      <el-input
                        type="textarea"
                        :rows="5"
                        placeholder=""
                        size="small" v-model="sendGoodsInfo.logisticsTips">
                      </el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
      
                <el-form-item>
                  <el-button type="primary" size="small" @click="getSendInfo"></el-button>
                  <el-button size="small" @click="showSendGoodsPopover = false"></el-button>
                </el-form-item>
              </el-form>
            </el-popover>
            <el-button class="filter-item" type="info" v-waves v-popover:sendGoodsPopover>
            </el-button>
            
            <el-button class="filter-item" type="danger" v-waves>
            </el-button>
            
            <el-button class="filter-item" type="success" v-waves @click="goBack">
            </el-button>
          </el-col>
        </el-row>
      </div>
      <!---->
      <div class="table-container">
        <el-table ref="goodTable" :data="goodList" element-loading-text="" fit highlight-current-row style="width: 98%;" show-summary :summary-method="getSummaries">
          <el-table-column align="center" width="100px" label="">
            <template scope="scope">
              {{scope.row.url}}
              <!-- <div v-for="item in imgUrl" :key="item" style="width: 60px;height: 60px;padding-top: 5px;">
                <img :src="item" />
              </div> -->
            </template>

          </el-table-column>
      
          <el-table-column align="center" prop="title" min-width="200px" label="" show-overflow-tooltip>
          </el-table-column>
      
          <el-table-column align="center" min-width="150px" prop="id" label="">
          </el-table-column>
      
          <el-table-column align="center" width="150px" prop="sku" label="">
          </el-table-column>
    
          <el-table-column align="center" width="100px" prop="quantity" label="">
          </el-table-column>
    
          <el-table-column align="center" width="100px" prop="unit" label="">
          </el-table-column>
          
          <el-table-column align="center" width="100px" prop="unitPrice" label="">
          </el-table-column>
    
          <el-table-column align="center" width="100px" prop="total" label="">
          </el-table-column>
        </el-table>
      </div>
      <!---->
      <div class="receive-container">
        <el-row class="receive-container-item">
          <el-col>
            <span style="color: #98a8b8;">&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span>: &nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span>: 13758087094&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span>: 3003&nbsp;&nbsp;&nbsp;&nbsp;</span>
          </el-col>
        </el-row>
        <el-row class="receive-container-item">
          <el-col>
            <span style="color: #98a8b8;">&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <span>2017-8-2 23:28</span>
          </el-col>
        </el-row>
        <el-row class="receive-container-item">
          <el-col :span="2">
            <span style="color: #98a8b8;">&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <i class="el-icon-plus" @click.prevent.stop="addRemark"></i>
          </el-col>
          <el-col :span="22">
            <el-row class="remark-item">
              
            </el-row>
            <el-row class="remark-item">
              
            </el-row>
            <el-row class="remark-item">
              
            </el-row>
            <el-row class="remark-item">
              
            </el-row>
          </el-col>
        </el-row>
      </div>
    </div>   
  </div>
</template>

<script>
  import { api } from '@/global/api'
  import { global } from '@/global/global'
  export default {
    name: 'OrderDetailPane',
    data() {
      return {
        infoLoading: false,
        showSendGoodsPopover: false,
        selectLogisticsCompany: [],
        sendGoodsInfo: {
          sendTime: '',
          logisticsCompany: '',
          logisticsNum: '',
          logisticsTips: ''
        },
        rules: {
          sendTime: [
            { type: 'date', required: true, message: '', trigger: 'blur' }
          ],
          logisticsCompany: [
            { type: 'array', required: true, message: '', trigger: 'blur' }
          ],
          logisticsNum: [
            { required: true, message: '', message: '', trigger: 'blur' }
          ]
        },
        goodList:[],
        imgUrl:[require("./../../assets/good1.jpg"),require("./../../assets/good2.jpg")]
      };
    },
    props: {
      orderId: {
        type: Number,
        default: 10023789420012
      }
    },
    filters: {
      statusFilter(status) {
        const statusMap = ['danger', 'success'];
        return statusMap[status];
      }
    },
    mounted() {
      var vm = this;
      vm.getGoodsList();
      vm.logisticsCompanyList();
    },
    methods: {
      //
      getGoodsList: function() {
        var vm = this;
        global.get( api.goods, null, function(response) {
            vm.goodList = response.body.data.goodList;
          }, function(response) {
            alert("");
        }, false)
      },
      //
      logisticsCompanyList: function() {
        var vm = this;
        global.get( api.logisticsCompany, null, function(response) {
          vm.selectLogisticsCompany = response.body.data.logisticsCompany;           
        }, function(response) {
            alert("");
        }, false)
      },
      //
      getSendInfo() {
        var vm = this;
        console.log(vm.sendGoodsInfo);
        if(vm.sendGoodsInfo.sendTime !== '' && vm.sendGoodsInfo.logisticsCompany !== '' && vm.sendGoodsInfo.logisticsNum !== ''){
          vm.$router.push( { path: '/index/order/showSendInfo', query: { sendGoodsInfo: vm.sendGoodsInfo } } );
        }else{
          showSendGoodsPopover = true;
        };
      },
      //
      getSummaries(param) {
        let { columns, data } = param;
        let sums = [];
        let excludeCol = [0, 1, 2, 3, 5];
        columns.forEach((column, index) => {
          if (index === 0) {
            sums[index] = '';
            return;
          }
          if (excludeCol.indexOf(index) !== -1) {
            sums[index] = '';
            return;
          }
          const values = data.map(item => Number(item[column.property]));
          sums[index] = values.reduce((prev, curr) => {
            const value = Number(curr);
            if (!isNaN(value)) {
              return prev + curr;
            } else {
              return prev;
            }
          }, 0);
        });
        return sums;
      }, 
      goBack() {
        this.$router.push('/index/order/orderAccount'); 
      }
    }
  };
</script>
<style scoped>
[v-cloak] {
  display: none;
}
.createPost-container {
  padding-left: 25px;
  padding-top: 30px;
}
.table-container {
  margin-top: 20px;
}
.receive-container {
  margin-top: 20px;
}
.receive-container>div {
  padding-left: 15px;
  margin-top: 15px;
}
</style>


