<template>
  <div class="customerTranfer">
    <div class="myChart">
       <div id="myChart" style="width:1000px;height:500px;"></div>
    </div>
    <div class="chartPie">
      <div id="chartPie" style="width: 500px;height: 500px;"></div>
   </div>
  </div>
</template>
<script>

 // 
let echarts = require('echarts/lib/echarts')
// 
//  require  import require  node_modules  import 
require('echarts/lib/chart/bar')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/title')


export default {
  name: 'hello',
  data() {
    return {
       chartPie: null
    }
  },
  mounted() {
      //
      // domecharts
      let myChart = this.$echarts.init(document.getElementById('myChart'))
      // 
      myChart.setOption({
        title: { text: '' },
        tooltip: {},
        xAxis: {
          data: ["", "", "", "", "", ""]
        },
        yAxis: {},
        series: [{
          name: '',
          type: 'bar',
          data: [1440, 2000, 3600, 2500, 4200, 1800]
        }]
      });
     
    /*ECharts*/
      var chartPie = echarts.init(document.getElementById('chartPie'));
        chartPie.setOption({
          title : {
            text: '',
            subtext: '',
            x:'center'
          },
          tooltip : {
            trigger: 'item',
            formatter: "{a} <br/>{b} : {c} ({d}%)"
          },
          legend: {
            orient: 'vertical',
            left: 'left',
            data: ['','','','','']
          },
          series : [
            {
              name: '',
              type: 'pie',
              radius: '55%',
              radius : '55%',
              center: ['50%', '60%'],
              data:[
                    {value:400, name:''},
                    {value:335, name:''},
                    {value:310, name:''},
                    {value:274, name:''},
                    {value:235, name:''}
              ],
          itemStyle: {
              emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }

        }
        ]
    })
  }

}
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  a {
    text-decoration: none;
  }
  .customerTranfer{
    position: absolute;
  }
  .customerTranfer .myChart{
    margin: 25px 30px;
 }
 .customerTranfer .chartPie{
    margin-top: 50px;
    margin-left: 105px;
 }
</style>


