<script>
    import {api} from '@/global/api'
    export default {
      data() {
        return {

          // --------------------------------------  ----------------------------------------------
          //tab
          activeName: 'first',

          //
          pickerOptions0: {
            disabledDate(time) {
               return time.getTime() < Date.now() - 8.64e7;
             }
          },

          //
          ruleForm: {
            jilu_date:'',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add:'',
            jilu_part:'',
            jilu_get: ''
          },

          //
          tableData: [{
            jilu_date: '',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add: '',
            jilu_part: '',
            jilu_get: 0
          }],


          // -----  ------
          dialogFormVisible: false,

          ModalsForm: {
            jilu_date:'',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add:'',
            jilu_part:'',
            jilu_get: ''
          },
          formLabelWidth: '120px',
          //----------------------


          //-----------------------------------------------------------------------------------------------------

          //
          rules: {
            jilu_input: [
              { required: true, message: '', trigger: 'blur' },
              { min: 3, max: 12, message: ' 3  12 ', trigger: 'blur' }
            ],
            pay_name: [
              { required: true, message: '', trigger: 'blur' },
              { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
            ],
            do_name: [
              { required: true, message: '', trigger: 'blur' },
              { min: 2, max: 10, message: ' 2  10 ', trigger: 'blur' }
            ],
            region: [
              { required: true, message: '', trigger: 'blur' }
            ],
            jilu_get: [
              // { required: true, message: '', trigger: 'blur'},
              // { value!=^[0-9]*$ , message: ''},

              // 
              //
              {validator:(rule,value,callback)=>{
                if (!value) {
                  return callback(new Error(''));
                }
                else if(/^\d+$/.test(value) == false){
                  callback(new Error(""));
                }else{
                  callback();
                }
              }, trigger:'blur'}

            ],

            jilu_charge: [
              // { required: true, message: '', trigger: 'blur'},
              // { value!=^[0-9]*$ , message: ''}

              // 
              //
              {validator:(rule,value,callback)=>{
                if (!value) {
                  return callback(new Error(''));
                }
                else if(/^\d+$/.test(value) == false){
                  callback(new Error(""));
                }else{
                  callback();
                }
              }, trigger:'blur'}

            ]
          },


          // ----------------------------------------  ---------------------------------------------
          //
          paragraphList: {
            jilu_date:'',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add:'',
            jilu_part:'',
            jilu_charge: ''
          },

          //
          tableData2: [{
            jilu_date: '',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add: '',
            jilu_part: '',
            jilu_charge: 0
          }],

          // 
          ModalsForm2: {
            jilu_date:'',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add:'',
            jilu_part:'',
            jilu_charge: ''
          },

          dialogFormVisible2: false,


          //----------------------------------------------------------------------------------------------




        };
      },
      mounted(){
        var vm = this;

        //json
        vm.getData();
        vm.getData2();
      },

      methods: {
        //tab
        handleClick(tab, event) {
          console.log(tab, event);
        },

        //
        getData(){
          this.$http.get(api.cwData_in,{params:{'key':this.jilu_input}}).then(function(response){ 
            // console.log(response);
            // console.log('json',response.data.tableList)
            this.tableData = response.data.tableList;
          },function(response){
            alert('')
          })
        },

        //
        getData2(){
          this.$http.get(api.cwData_out).then(function(response){ 
            // console.log(response);
            // console.log('json',response.data.tableList2)
            this.tableData2 = response.data.tableList2;
          },function(response){
            alert('')
          })
        },


//--------  -------
        // @change 
        //
        setDate(val) {
          this.ruleForm.jilu_date=val;
          // this.tableData.jilu_date=val;
          // this.ModalsForm.jilu_date=val;
        },

        //
        setDate2(val){
          this.ModalsForm.jilu_date=val;
          
          this.tableData.jilu_date = this.ModalsForm.jilu_date;
          // console.log(this.ModalsForm.jilu_date)
          // console.log(this.tableData.jilu_date)
        },

        //ruleForm
        initForm(){
          let vm = this;
          vm.ruleForm = {
            jilu_date:'',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add:'',
            jilu_part:'',
            jilu_get: ''
          }
        },


        //
        submitForm(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              var vm = this;
              var flag = 0;
              console.log('',vm.ruleForm);

              // 
              for(var addlist of vm.tableData){
                if(vm.ruleForm.jilu_input == addlist.jilu_input){
                  alert("")
                  flag = 1;
                  return flag;
                  // break;
                }
              }
              
              if(flag!=1){
              // 
              vm.tableData.push(vm.ruleForm)
              console.log('',vm.tableData)
              
              // this.dialogFormVisible = false;

              alert('!');
              this.initForm();
              }

            } else {
              console.log('!!');
              return false;
            }
          });

        },

        // 
        resetForm(formName) {
          this.$refs[formName].resetFields();
          //
          this.initForm();
        },


        // 
        deleteRow(index, rows) {
          rows.splice(index, 1);
        },

        // 
        editData(row,_index){
          // alert(0);
          let vm = this;
          //
          vm.dialogFormVisible = true;
          
          // vm.ModalsForm.push(vm.tableData);
          // console.log("",vm.ModalsForm);

          //
          this.listIndex=_index;
          //
          // this.ModalsForm=row;
          // this.ModalsForm=JSON.parse(JSON.stringify(row));

          //
          this.ModalsForm=Object.assign({}, row);
        },

        // 
        submitEdit(formName) {
          // var set = this;
          this.$refs[formName].validate((valid) => {
            if (valid) {
              let _index=this.listIndex;
              //list
              this.tableData[_index]=this.ModalsForm;
              //
              this.tableData.splice(_index,1,this.ModalsForm);

              alert('!');
              //
              this.dialogFormVisible=false;
            } else {
              console.log('error submit!!');
              return false;
            }
          });

        },

        //ModalsForm
        initForm2(){
          let vm = this;
          // let _index=this.listIndex;
          vm.ModalsForm = {
            jilu_date:'',
            jilu_input: '',
            pay_name: '',
            do_name: '',
            region: '',
            jilu_add:'',
            jilu_part:'',
            jilu_get: ''
          }
        },

        //
        resetEdit(formName) {
          this.$refs[formName].resetFields();
          this.initForm2();
        },
    //-----------------------------------


    //------------------  ----------------
    // @change 
    //
    p_setDate(val) {
      this.paragraphList.jilu_date=val;
    },

    //
    p_setDate2(val) {
      this.ModalsForm2.jilu_date=val;
      
      this.tableData2.jilu_date = this.ModalsForm2.jilu_date;
    },


    //paragraphList
    initForm3(){
      let vm = this;
      vm.paragraphList = {
        jilu_date:'',
        jilu_input: '',
        pay_name: '',
        do_name: '',
        region: '',
        jilu_add:'',
        jilu_part:'',
        jilu_charge: ''
      }
    },


    //
    submitForm2(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          var vm = this;
          var flag = 0;
          console.log('',vm.paragraphList);

          // 
          for(var addlist2 of vm.tableData2){
            if(vm.paragraphList.jilu_input == addlist2.jilu_input){
              alert("")
              flag = 1;
              return flag;
              // break;
            }
          }

          if(flag!=1){
          // 
          vm.tableData2.push(vm.paragraphList)
          console.log('',vm.tableData2)
          
          // this.dialogFormVisible = false;

          alert('!');
          this.initForm3();
          }
        } else {
          console.log('error submit!!');
          return false;
        }
      });

    },



    // 
    resetForm2(formName) {
      this.$refs[formName].resetFields();
      //
      this.initForm3();
    },



      // 
      editData2(row,_index){
        // alert(0);
        let vm = this;
        //
        vm.dialogFormVisible2 = true;

        //
        this.listIndex=_index;
        //
        // this.ModalsForm2=row;
        //
        this.ModalsForm2=Object.assign({}, row);
      },

      // 
      submitEdit2(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            // alert(1);
            let _index=this.listIndex;
            //list
            this.tableData2[_index]=this.ModalsForm2;
            //
            this.tableData2.splice(_index,1,this.ModalsForm2);

            alert('!');
            this.dialogFormVisible2=false;
          } else {
            console.log('error submit!!');
            return false;
          }
        });

      },

      //ModalsForm
      initForm4(){
        let vm = this;
        // let _index=this.listIndex;
        vm.ModalsForm2 = {
          jilu_date:'',
          jilu_input: '',
          pay_name: '',
          do_name: '',
          region: '',
          jilu_add:'',
          jilu_part:'',
          jilu_charge: ''
        }
      },

      //
      resetEdit2(formName) {
        this.$refs[formName].resetFields();
        this.initForm4();
      },



    //------------------------------------------



      }
    };
</script>







