<template>
  <div class="post">
    <div class="post1">
    <div class="one">
      <el-tabs type="border-card">

        <el-tab-pane label="">
          <el-row :gutter="20">
            <el-col :span="8">                
                <div class="user_img" >
                    <!-- <img :src="ImgUrl" alt="" > -->
                    <el-tooltip class="item" effect="dark" content="" placement="bottom-start">   
                        <el-upload class="avatar-uploader" action="https://jsonplaceholder.typicode.com/posts/" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
                          <img v-if="imageUrl" :src="imageUrl" class="avatar"><!--  -->
                          <img v-else :src="ImgUrl" class="avatar"><!--  -->
                          <!-- <i v-else class="el-icon-plus avatar-uploader-icon"></i> -->
                        </el-upload>
                    </el-tooltip>
                </div>               
            </el-col>
            
            <el-col :span="16">
                <div class="user_info">
                    <h2> <el-button size="small" v-if="!editable" @click="editable = true" style="float:right;" icon="edit"></el-button> </h2>
                    <div class="user_info_form" v-if="editable">
                      
                        <el-form label-position="right" label-width="100px" :model="personalInfo">
                              <el-form-item label=":">
                                <el-input v-model="personalInfo.name"></el-input>
                              </el-form-item>
                              <el-form-item label=":">
                                <el-input v-model="personalInfo.Size"></el-input>
                              </el-form-item>
                              <el-form-item label=":" >
                                <el-input v-model="personalInfo.address"></el-input>
                              </el-form-item>
                              <el-form-item label=":">
                                <el-input type="textarea" v-model="personalInfo.introduction"></el-input>
                              </el-form-item>
                              <el-form-item label=":">
                                <el-input v-model="personalInfo.price"></el-input>
                              </el-form-item>
                              <el-form-item>
                                 <el-button type="primary" @click="submitForm1()"></el-button>
                                 <el-button @click="resetForm()"></el-button>
                               </el-form-item>
                        </el-form>

                    </div>

                    <div class="user_info_text" v-else>
                        <el-form label-position="right" label-width="100px" :model="personalInfo">
                              <el-form-item label=":">
                                {{ personalInfo.name }}
                              </el-form-item>
                              <el-form-item label=":">
                                {{ personalInfo.Size }}                              
                              </el-form-item>
                              <el-form-item label=":">
                                {{ personalInfo.address }}                               
                              </el-form-item>
                              <el-form-item label=":">
                                {{ personalInfo.introduction }}                               
                              </el-form-item>
                              <el-form-item label=":">
                                {{ personalInfo.price }}                              
                              </el-form-item>                            
                        </el-form>
                    </div>
                </div>

            </el-col>

          </el-row>
        </el-tab-pane>

        <el-tab-pane label="">
             <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
               <el-form-item label="">
               <el-input v-model="ruleForm.name"></el-input>
             </el-form-item>
             <el-form-item label="" required>
               <el-col :span="11">
                 <el-form-item >
                   <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
                 </el-form-item>
               </el-col>
               <el-col class="line" :span="1">-</el-col>
               <el-col :span="11">
                 <el-form-item >
                   <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
                 </el-form-item>
               </el-col>
             </el-form-item>
             <el-form-item label="">
               <el-select v-model="ruleForm.region" placeholder="">
                 <el-option
                   v-for="item in regoin"
                   :key="item.key"
                   :label="item.value"
                   :value="item.key">
                 </el-option>
               </el-select>
             </el-form-item>

             <el-form-item label="">
               <el-select v-model="ruleForm.address" placeholder="">
                 <el-option
                   v-for="item in options2"
                   :key="item.key"
                   :label="item.value"
                   :value="item.key">
                 </el-option>
               </el-select>
             </el-form-item>
             <el-form-item label="">
               <el-switch on-text="" off-text="" v-model="ruleForm.delivery"></el-switch>
             </el-form-item>
             <el-form-item label="">
               <el-checkbox-group v-model="ruleForm.type">
                 <el-checkbox label="" name="type"></el-checkbox>
                 <el-checkbox label="" name="type"></el-checkbox>
                 <el-checkbox label="" name="type"></el-checkbox>
                 <el-checkbox label="" name="type"></el-checkbox>
                 <el-checkbox label="" name="type"></el-checkbox>
               </el-checkbox-group>
             </el-form-item>
             <el-form-item label="">
               <el-radio-group v-model="ruleForm.resource">
                 <el-radio label=""></el-radio>
                 <el-radio label=""></el-radio>
               </el-radio-group>
             </el-form-item>
             <el-form-item label="">
               <el-input type="textarea" v-model="ruleForm.desc"></el-input>
             </el-form-item>
             <el-form-item>
               <el-button type="primary" @click="submitForm2('ruleForm')"></el-button>
               <el-button @click="resetForm('ruleForm')"></el-button>
             </el-form-item>
           </el-form>
        </el-tab-pane>
      </el-tabs>
    </div>
    </div>
  </div>
</template>
<script>
  import Img from '../../assets/light02.jpg' //
  import { mapGetters } from 'vuex';         //
  import {api} from '@/global/api'
  export default {
      data () {
          return {
             ImgUrl:Img,
             imageUrl:'',
             editable:false,
             personalInfo:{
              "name":"",
              "Size":"34*43",
              "address": "44",
              "introduction":",",
              "price":"50"
             },
             ruleForm: {
               name: '',
               region: '',
               date1: '',
               date2: '',
               delivery: false,
               type: [],
               resource: '',
               desc: '',
               address:''
             },
             rules: {
               name: [
                 { required: true, message: '', trigger: 'blur' },
                 { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
               ],
               region: [
                 { required: true, message: '', trigger: 'change' }
               ],
               date1: [
                 { type: 'date', required: true, message: '', trigger: 'change' }
               ],
               date2: [
                 { type: 'date', required: true, message: '', trigger: 'change' }
               ],
               type: [
                 { type: 'array', required: true, message: '', trigger: 'change' }
               ],
               resource: [
                 { required: true, message: '', trigger: 'change' }
               ],
               desc: [
                 { required: true, message: '', trigger: 'blur' }
               ]
             },
             options2: [
               {key:"01", value:""},{key:"02", value:""}
               ],
             props: {
               value: 'label',
               children: 'cities'
             },
             regoin: [{key:"01", value:""},{key:"02", value:""}],
          };
      },
      computed: {
            ...mapGetters({
                personalInfo_init:'userInfo'
            })
      },
      mounted() {
        var vm = this;
        vm.resetForm();
      },
      methods: {
        //
        handleAvatarSuccess(res, file) {
          this.imageUrl = URL.createObjectURL(file.raw);
        },
        beforeAvatarUpload(file) {
          const isJPG = file.type === 'image/jpeg';
          const isLt2M = file.size / 1024 / 1024 < 2;

          if (!isJPG) {
            this.$message.error(' JPG !');
          }
          if (!isLt2M) {
            this.$message.error(' 2MB!');
          }
          return isJPG && isLt2M;
        },
        //
        submitForm1() {
            var vm = this;
            vm.editable = false;
            var par = {
                "account":  vm.personalInfo.account,
                "name": vm.personalInfo.name,
                "Size": vm.personalInfo.Size ,
                "sex": vm.personalInfo.sex,
                "address": vm.personalInfo.address,
                "introduction": vm.personalInfo.introduction,
            };

            console.log('',par)
        },
        //
        resetForm() {
            var vm = this;
            vm.editable = false;
            // vm.personalInfo = JSON.parse( JSON.stringify(vm.personalInfo_init) ) ;
        },
        //
        submitForm2(formName) {
          this.$refs[formName].validate((valid) => {
            if (valid) {
              alert('submit!');
            } else {
              console.log('error submit!!');
              return false;
            }
          });
        },
        //
        // resetForm(formName) {
        //   this.$refs[formName].resetFields();
        // },
        handleItemChange(val) {
          console.log('active item:', val);
          setTimeout(_ => {
            if (val.indexOf('') > -1 && !this.options2[0].cities.length) {
              this.options2[0].cities = [{
                label: ''
              }];
            } else if (val.indexOf('') > -1 && !this.options2[1].cities.length) {
              this.options2[1].cities = [{
                label: ''
              }];
            }
          }, 300);
        }
            
      }
  }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  a {
    text-decoration: none;
  }
  .post1 .one{
    margin: 20px;
  }
  /*.user_img img{
    max-width: 200px;
    max-height: 200px;
  }*/

  /**/
  .post1 .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
  .post1 .avatar-uploader .el-upload:hover {
      border-color: #20a0ff;
  }
  .post1 .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 178px;
      height: 178px;
      line-height: 178px;
      text-align: center;
  }
  .post1 .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .post1 .el-tab-pane{
    width: 900px;
    height: 530px;
  }
  /**/
  .post1 .item {
    margin: 0px;
  }
  /**/
  .post1 .user_info{
    padding-bottom: 20px;
    border: 1px solid #dee1e2;
  }
  .post1 h2{
    margin: 5px;
    padding: 10px 20px;
    border-bottom: 1px solid #dee1e2;            
  }
  .post1 .i_edit{
    float: right;
    font-size: 16px;
    color: #7ab8ed;
  }
  .post1 .user_info_form,.post1 .user_info_text{
      padding: 20px;
  }

</style>



