import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

//readme
import Readme from '@/view/index/readme'

//
import Login from '@/view/index/login'
import Index from '@/view/index/index'

//
import User from '@/view/user/a-user'
import UserInfo from '@/view/user/userInfo'
import NewUser from '@/view/user/new-user'
import UserEdit from '@/view/user/userEdit'
import Power from '@/view/user/power'
import Sale from '@/view/user/sale'
import Echarts from '@/view/user/echarts'

//
import House from '@/view/warehouse/a-house'
// import Instock from '@/view/warehouse/instock'
// import Outstock from '@/view/warehouse/outstock'
import Allocation from '@/view/warehouse/allocation'
import Remaining from '@/view/warehouse/remaining'
import InOutstock from '@/view/warehouse/inOutstock'

//
import Product from '@/view/product/a-product'
import Chengben from '@/view/product/product-chengben'
import Kucun from '@/view/product/product-kucun'
import Shuxing from '@/view/product/product-shuxing'
import KucunChakan from '@/view/product/kucun-chakan'
import ProductList from '@/view/product/productList'
import AddProduct from '@/view/product/addProduct'

//
import Finance from '@/view/finance/a-finance'
import Jilu from '@/view/finance/jilu'
import TimeProfits from '@/view/finance/timeProfits'
import F_Echarts from '@/view/finance/echart'

//
import Customer from '@/view/customer/a-customer'
import CustomerVip from '@/view/customer/customerVip'
import CustomerInfo from '@/view/customer/customerInfo'
import CustomerOder from '@/view/customer/customerOder'
import CustomerTranfer from '@/view/customer/customerTranfer'

//
import Order from '@/view/order/a-order'
import OrderIndex from '@/view/order/index'
import OrderAccount from '@/view/order/orderAccount'
import EditOrder from '@/view/order/editOrder'
import ShowSendInfo from '@/view/order/showSendInfo'
import OrderColleague from '@/view/order/orderColleague'
import DayThree from '@/view/order/dayThree'

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    { 
      path: '/login', 
      component: Login, 
      name: '' 
    },
    { 
      path: '/index',
      component: Index,
      children: [
        {
          path: 'readme', 
          component: Readme, 
          name: ''
        },
        { 
          path: 'user', 
          component: User,
          children: [
            { path: 'userInfo', component: UserInfo, name: ''  },
            { path: 'userEdit', component: UserEdit, name: ''  },
            { path: 'newUser', component: NewUser, name: ''  },
            { path: 'power', component: Power, name: ''  },
            { path: 'sale', component: Sale, name: ''  },
            { path: 'echarts', component: Echarts, name: ''  }
          ]
        },
        { 
          path: 'house', 
          component: House,
          children: [
            // { path: 'instock', component: Instock, name: ''  },
            // { path: 'outstock', component: Outstock, name: ''  },
            { path: 'inOutstock', component: InOutstock, name: ''  },
            { path: 'allocation', component: Allocation, name: ''  },
            { path: 'remaining', component: Remaining, name: ''  }
          ]
        },
        { 
          path: 'product', 
          component: Product,
          children: [
            { path: 'chengben', component: Chengben, name: ''  },
            { path: 'kucun', component: Kucun, name: ''  },
            { path: 'shuxing', component: Shuxing, name: ''  },
            { path: 'kucun-chakan', component: KucunChakan, name: '' },
            { path: 'productList', component: ProductList, name: '' },
            { path: 'addProduct', component: AddProduct, name: '' }
          ]
        },
        { 
          path: 'finance', 
          component: Finance,
          children: [
            { path: 'jilu', component: Jilu, name: ''  },
            { path: 'timeProfits', component: TimeProfits, name: '' },
            { path: 'echart', component: F_Echarts, name: ''  }
          ]
        },
        { 
          path: 'customer', 
          component: Customer,
          children: [
            { path: 'customer-vip', component: CustomerVip, name: '' },
            { path: 'customerInfo', component: CustomerInfo, name: ''  },
            { path: 'customerOder', component: CustomerOder, name: ''  },
            { path: 'customerTranfer', component: CustomerTranfer, name: ''  }
          ]
        },
        { 
          path: 'order', 
          component: Order,
          children: [
            { path: 'index', component: OrderIndex, name: '' },
            { path: 'orderAccount', component: OrderAccount, name: '' },
            { path: 'editOrder', component: EditOrder, name: '' },
            { path: 'showSendInfo', component: ShowSendInfo, name: '' },
            { path: 'orderColleague', component: OrderColleague, name: '' },
            { path: 'dayThree', component: DayThree, name: '' }
          ]
        },
      ]
    },    
  ]
})



