<template>
  <div class="userInfo">
    <!--  ()-->
    <!--  <div class="bre-crumb">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>  
        </el-breadcrumb>
     </div> -->
    <!--  -->
    <div class="info-top">
      <el-form :inline="true" v-model="list" class="demo-form-inline" >

        <el-form-item>
        <el-form-item label=""><!--  -->
          <el-input @keyup.enter.native="handleFilter" v-model="listQuery.employName" placeholder=""></el-input>
        </el-form-item>
        <el-button type="primary" v-waves icon="search" @click="handleFilter"></el-button>
        <el-button type="primary" v-waves icon="edit" @click="handleCreate"></el-button>
        <el-button type="primary" v-waves icon="edit" @click="handleDelAll"></el-button>
        <el-button type="primary" v-waves icon="document" @click="handleDownload"></el-button>

        </el-form-item>
      </el-form>
    </div>
    
    <!--  -->
    <div class="add">
      <el-dialog title="" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.employName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.sex" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="form.employType" placeholder="">
              <el-option v-for="item in  typeOptions" :key="item.key" :label="item.display_name+'('+item.key+')'" :value="item.key">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.phoneNumber" auto-complete="off"></el-input>
          </el-form-item>
           <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="form.entrydate" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button v-waves @click="dialogFormVisible = false"> </el-button>
          <el-button v-waves type="primary" @click="handleCreateSubmit"> </el-button>
        </div>
      </el-dialog>
      <router-view></router-view>
    </div>

    <!--  -->
    <div class="info-con"> 
      <el-table ref="multipleTable" :data="list" border tooltip-effect="dark" style="width: 100%"
       @selection-change="handleSelectionChange" v-loading.body="listLoading" element-loading-text="" border fit highlight-current-row>
          <el-table-column type="selection" width="55"></el-table-column>
         <!--  <el-table-column v-model="form.id" prop="id" label="ID" width="60"></el-table-column> -->
          <el-table-column align="center" label='' width="">
            <template scope="scope">
              {{scope.$index+1}}
            </template>
          </el-table-column>

          <!-- <el-table-column v-model="form.name" prop="name" label="" width="120"></el-table-column> -->
          <el-table-column label="" align="center" width="100">
            <template scope="scope">
              {{scope.row.employName}}
            </template>
          </el-table-column>
          
          <el-table-column label=""  align="center" width="100">
            <template scope="scope">
              <span v-if="scope.row.sex == ''"></span>
              <span v-else></span>
            </template>
          </el-table-column>

          <el-table-column label=""  align="center" width="100">
            <template scope="scope">
              <span v-if="scope.row.employType == '001'"></span>
              <span v-if="scope.row.employType == '002'"></span>
            </template>
          </el-table-column>

          <el-table-column align="center"  label="" width="150">
                <template scope="scope">
                  <span>{{scope.row.phoneNumber}}</span>
                </template>
            </el-table-column>
          <!-- <el-table-column v-model="form.address" prop="address" label="" width="240" show-overflow-tooltip></el-table-column> -->
          <el-table-column align="center"  label="" width="150">
                <template scope="scope">
                  <span>{{scope.row.address}}</span>
                </template>
            </el-table-column>
          <!-- <el-table-column v-model="form.date" label="" width="120"> <template scope="scope">{{ scope.row.date }}</template></el-table-column> -->
          <el-table-column align="center"  label="" width="120">
                <template scope="scope">
                  <span>{{scope.row.entrydate | formatDate}}</span><!--  -->
                </template>
            </el-table-column>

          <el-table-column label="" align="center" width="150">
            <template scope="scope">
              <el-button v-waves size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button v-waves size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
      </el-table>

      <div style="margin-top: 20px">
        <el-button v-waves @click="toggleSelection([list[0], list[1],list[2],list[3],list[4],list[5],list[6]])"></el-button>
        <el-button v-waves @click="toggleSelection()"></el-button>
      </div>
    </div>

<!--  -->
  <!--   <div class="edit">
      <el-dialog title="" :visible.sync="dialogFormVisible1">
        <el-form :model="editForm">
          <el-form-item label="ID" :label-width="formLabelWidth">
              <el-input v-model="editForm.id" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.name" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="editForm.type" placeholder="">
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.phoneNumber" auto-complete="off"></el-input>
          </el-form-item>
           <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.date" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
         
            <el-button v-waves @click="handledoneEdit('ruleForm')"></el-button>
            <el-button v-waves type="primary" @click="dialogFormVisible1 = false"></el-button>
          
        </div>
      </el-dialog>
      <router-view></router-view>
    </div>
 -->

    <!--  -->
    <div class="info-footer">
        <div class="block" v-show="!listLoading">
           <span class="demonstration"></span>
           <el-pagination
             @size-change="handleSizeChange"
             @current-change="handleCurrentChange"
             :current-page.sync="listQuery.currPage"
             :page-sizes="[10, 20, 30, 50]"
             :page-size="listQuery.pageSize"
             layout="total, sizes, prev, pager, next, jumper"
             :total="total">
           </el-pagination>
         </div>

    </div>

  </div>
 
</template>

<script>
     import {api} from '@/global/api'
     import {global} from '@/global/global';
     import { Message } from 'element-ui';
     import {formatDate} from '@/filters/index'; //
     import {export_json_to_excel} from '@/vendor/Export2Excel';//

     export default {
       data() {
         return {
           
           list:null,//
           // 
           total:null,
           listLoading:true,
           listQuery:{
              currPage:1,
              pageSize:10,
              employName:''
           },
           //
           form: {
                 "sex": "",
                 "employName": "",
                 "employType": "",
                 "phoneNumber": "",
                 "address": "",
                 "entrydate": "",
                 "orderNum": 10
           },
           //
          // editForm: {
          //        "sex": "",
          //        "employName": "",
          //        "employType": "",
          //        "phoneNumber": "",
          //        "address": "",
          //        "entrydate": "",
          //        "orderNum": 10
          // },
           typeOptions:[
              {key:'001',display_name:''},
              {key:'002',display_name:''}
           ],
           formLabelWidth: '120px',
           //JSon
           dialogFormVisible: false,
           dialogFormVisible1:false,
           multipleSelection: [],//
          // 
          

         }
       },

       //--
       filters:{
          formatDate
       },

       mounted(){
        var vm=this;
        vm.getList();//

        //
        // vm.getFormData();

       },
       methods: {
        //--
        getList(){
          // console.log(66666666666666666666666)
            let vm = this;

            vm.listLoading = true;

          global.get( api.queryUser,{params: vm.listQuery},function(res){
               console.log('----',JSON.stringify(res) )
              let data = res.body;
              if(data.resultCode == 0){//
                console.log("------",data.resultCode)
                vm.list = data.data.data;
                // console.log('',vm.list);
                vm.listQuery.currPage = data.data.currPage;
                vm.listQuery.pageSize = data.data.pageSize;
                vm.total = data.data.tatal;

                vm.listLoading = false;
                // console.log(66666666666666666666666)
              }else{
                Message({
                  showClose:true,
                  message:res.body.resultMsg,
                  type:'error'
                });
                vm.listLoading = false;
              }
          },function(res){
            alert("");
            vm.listLoading = false;
          },false)

         },

        //  //---id
        // getFormData:function(){
        //   var vm = this;
        //   var data = this.$route.query;
        //   console.log('url',data);
        //   //urlajax
        //   global.get(api.queryUserItem,{params:{'id':data.id}},function(res){
        //     console.log('-------id',JSON.stringify(res) );
        //     if(res.body.resultCode == 0){
        //       var res = res.body.data;
        //       console.log('=====',res);
        //       vm.editForm = res
        //     }else{
        //       //alert(res.body.resultMsg)
        //                     Message({
        //                         showClose: true,
        //                         message: res.body.resultMsg,
        //                         type: 'error'
        //                     });
        //     }
        //   },function(res){
        //     //
        //   },true)
        // },
      
         // 
         handleCreate(){
          /* 
           //
           this.initForm();
           this.dialogFormVisible = true;
          */
          //
          //
          this.$router.push('/index/user/newUser')//index.jspath 2018.4.25

         },
         // 
         handleCreateSubmit(){
            var vm = this;
            console.log('',vm.form);
            // 
            vm.list.push(vm.form);//
            console.log('',vm.list);
            this.dialogFormVisible = false;
         },
         //
         handleEdit(index, row) {
            
            this.dialogFormVisible1 = true;
            let vm = this;
            // console.log("",index, "row",row);
            this.$router.push({path:'./userEdit',query:{id:row._id}});//
         },
        // //
        // handledoneEdit(formName){
        //   let that = this;

        //   this.$refs[formName].validate((valid) => {
        //     if(valid){
        //       console.log('',this.editForm);

        //       global.post('http://localhost:3000/api/modifyUser',this.editForm,null,function(res){
        //         //alert('',res)
                          
        //                   //
        //                   Message({
        //                       showClose: true,
        //                       message: '',
        //                       type: 'success'
        //                   })
        //                   setTimeout(()=>{
        //                     that.$route.push('./userInfo')
        //                   },2000)

        //       },function(res){
        //         alert('',res)
        //       })
        //     } else{
        //       console.log('error submit!!');
        //       return false;
        //     }
        //   });
        // },

         //
         handleDelete(index, row) {  
            let vm = this;
            console.log("index",index,"row",row);
            //
            // this.list.splice(index,1);
            this.$confirm(', ?', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(() => {
          // console.log("qqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqqq")//2018.2.26  
          vm.delFun(row);

            this.$message({
              type: 'success',
              message: '!'
            });
        }).catch( () => {
            // this.$message({
            //   type: 'info',
            //   message: ''
            // });          
        });
         },
         //
         delFun(row){
           let vm = this;

           global.get( api.delUser,{params: {'id':row['_id']} },function(res){//get
            console.log("del")//
           console.log('-------',JSON.stringify(res) )
           vm.getList();  
                         
           },function(res){
               alert('')
               vm.listLoading = false;
           },false)
         },
         //
         handleDelAll(){
            let vm = this;
            console.log("row",vm.multipleSelection);//
            //splice vm.multipleSelection forid[1,3,5]
            this.$confirm(', ?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                //
                for (var index in vm.multipleSelection) {
                    console.log('======',vm.multipleSelection[index])
                    vm.delFun(vm.multipleSelection[index]);
                };
                

                this.$message({
                  type: 'success',
                  message: '!'
                });


            }).catch(() => {
                // this.$message({
                //   type: 'info',
                //   message: ''
                // });          
            });
         },
         //
         handleDownload(){
            // console.log("");
            let vm = this;

            require.ensure([], () => {
              // const { export_json_to_excel } = require('vendor/Export2Excel');
              const tHeader = ['', '', '', '', '',''];
              const filterVal = ['employName', 'sex', 'employType', 'phoneNumber', 'address','entrydate'];
              const list = vm.list;
              const data = vm.formatJson(filterVal, list);
              export_json_to_excel(tHeader, data, 'excel');
            })
         },
         formatJson(filterVal, jsonData) {
           return jsonData.map(v => filterVal.map(j => v[j]))
         },

         //
         toggleSelection(rows) {
           if (rows) {
             rows.forEach(row => {
               this.$refs.multipleTable.toggleRowSelection(row);
             });
           } else {
             this.$refs.multipleTable.clearSelection();
           }
         },
         //
         handleSelectionChange(val) {
           this.multipleSelection = val; //[]
         },
         //
         handleFilter() {
           this.getList();
         },
         //
         handleSizeChange(val) {
          // console.log(` ${val} `);
          this.listQuery.pageSize = val;
          this.getList();
         },
        //
        handleCurrentChange(val) {
          // console.log(`: ${val}`);
          //console.log('--------',val)
          this.listQuery.currPage = val;
          this.getList();
         },



       }
     }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  .userInfo{
    position: absolute;
  }
  .info-top{
    margin: 20px 10px;
  }
  .demo-form-inline{
    margin-left: 50px;
  }
  .info-con{
    margin: 20px 10px;
  }
 
</style>


