<!---->
<template>
  <div class="colleague-container">
    <!--  -->
    <div class="filter-container">
      <el-input style="width: 200px;" class="filter-item" placeholder="" v-model="listQuery.colleagueId">
      </el-input>

      <el-input style="width: 200px;" class="filter-item" placeholder="" v-model="listQuery.colleagueName">
      </el-input>
      <el-button class="filter-item" type="primary" v-waves icon="search" @click="handleFilter"></el-button>
      <el-button class="filter-item" type="primary" v-waves icon="edit" @click="handleDelAll"></el-button>
      <el-button class="filter-item" type="primary" v-waves icon="document" @click="handleDownload"></el-button>    
    </div> 

    <!--  -->
    <el-table ref="multipleTable" @selection-change="handleSelectionChange" :data="list" v-loading.body="listLoading" element-loading-text="" border fit highlight-current-row  >
        <el-table-column type="selection" width="55">
        </el-table-column>

        <el-table-column label="">
            <template scope="scope">
              {{scope.row.colleagueId}}
            </template>
        </el-table-column>

        <el-table-column align="center"  label="">
            <template scope="scope">
           		<span>{{scope.row.colleagueName}}</span>
            </template>
        </el-table-column>

        <el-table-column align="center"  label="">
            <template scope="scope">
            <span>{{scope.row.orderId}}</span>
            </template>
        </el-table-column>

        <el-table-column align="center"  label="">
            <template scope="scope">
            <span>{{scope.row.orderDay}}</span>
            </template>
        </el-table-column>

		<el-table-column align="center"  label="">
		    <template scope="scope">
		      <span>{{scope.row.money}}</span>
		    </template>
		</el-table-column>

        <el-table-column label="" align="center">
            <template scope="scope">
            	<el-tag :type="scope.row.orderStatus | statusFilter">{{scope.row.orderStatus | statusFilterTip}}</el-tag>
            </template>
        </el-table-column>

        <el-table-column align="center"  label="">
            <template scope="scope">
              <span>{{scope.row.auditor}}</span>
            </template>
        </el-table-column>

        <el-table-column align="center"  label="" width="150">
            <template scope="scope">
               <el-button class="edit" size="small" v-waves @click="handleEdit(scope.$index, scope.row)"></el-button>
               <el-button class="delete" size="small" v-waves type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </template>
        </el-table-column>
    </el-table>

    <!--  -->
    <div v-show="!listLoading" class="pagination-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.currPage"
        :page-sizes="[10,20,30, 50]" :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
	//       	
  import { api } from '@/global/api'
  import { global } from '@/global/global'
  export default {
    data() {
      return {
        list: null,//list
        total: null,
        listLoading: false,
        checkAllStatus: true,
        checkedStatuss: [0, 1],
        listQuery: { //
          currPage: 1,
          pageSize: 10,
          colleagueId: '', 
          colleagueName: ''
        },
        multipleSelection: [],  //
        orderStatus: [{label: '', value: 1}, {label: '', value: 2}, {label: '', value: 3}, {label: '', value: 4}, {label: '', value: 5}],
      }
    },
    filters: {
        parseTime(time) {
        	return parseTime(time);
        },
        statusFilter(status) {
            const statusMap = ['primary', 'success', 'warning', 'danger', 'gray'];
            return statusMap[status - 1];
        },
        statusFilterTip(status) {
            const statusMap = ['', '', '', '', ''];
            return statusMap[status - 1];
        }
    },
    mounted() {
        var vm = this;
        vm.getList();
    },
    methods: {
      //
      getList () {
      	var vm = this;
        //
        console.log(JSON.stringify("=============",vm.orderIdtemp));
        global.get( api.orderColleague, {params: vm.orderIdtemp}, function(response) {
            var data = response.body;
            vm.list = data.data.data;
            vm.listQuery.currPage = data.data.currPage;
            vm.listQuery.pageSize = data.data.pageSize;
            vm.total = data.data.total;
            vm.listLoading = false;   
          }, function(response) {
            alert("");
      	}, false)
   	  },
      //
      handleEdit (index,row){
        var vm = this;
        console.log('row',index,'-----',row);
        //
        this.$router.push( { path: '/index/order/editOrder', query: { id: row.orderId } } ); //
      },
      //
      handleDelete(index,row){
        var vm = this;
        // vm.handleDelAll()
        //console.log('row',index,'-----',row);
        //
        vm.list.splice(index,1)
      },
      //
      handleDelAll(){
        var vm = this;
        vm.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        //console.log('row',vm.multipleSelection);
      },
      //
      handleFilter() {
        //
      	//this.getList();
        var vm = this;
        vm.$message({
          showClose: true,
          message: '' + vm.listQuery.colleagueId + ' ;  ' + vm.listQuery.colleagueName,
          type: 'success'
        });
      },
      //
      handleSizeChange(val) {
        this.listQuery.pageSize = val;
        this.getList();
      },
      //
      handleCurrentChange(val) {
        //console.log('--------',val)
        this.listQuery.currPage = val;
        this.getList();
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
      },
      //
      handleDownload() {
        var vm = this;
        require.ensure([], () => {
          const { export_json_to_excel } = require('../../vendor/Export2Excel');
          const tHeader = ['', '', '', '', '', '', ''];
          const filterVal = ['colleagueId', 'colleagueName', 'orderId', 'orderDay', 'money', 'orderStatus', 'auditor'];
          const list = vm.list;
          const data = vm.formatJson(filterVal, list);
          export_json_to_excel(tHeader, data, 'excel');
        })
      },
      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
      }
    }
  };
</script>
<style>
  .colleague-container {
    width: 1210px;
    padding-left: 20px;
    padding-top: 15px;
  } 
  @media only screen and (max-width: 1400px) {
    .colleague-container {
      width: 1160px;
      padding-top: 20px;
    } 
  }
  .colleague-container .el-table {
    margin-top: 20px;
  } 
  .colleague-container .edit {
    float: left;
  }
  .colleague-container .delete {
    float: right;
  }
  .colleague-container .pagination-container {
    margin-top: 15px;
  }
  .colleague-container .top-distance {
    margin-top: 15px;
  }
</style>


