<template>
  <div class="new-user">
    <!-- <h1>{{ msg }}</h1> -->
    <!--  -->
   <!--  <div class="bre-crumb">
       <el-breadcrumb separator="/">
         <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
         <el-breadcrumb-item></el-breadcrumb-item>  
       </el-breadcrumb>
    </div> -->

    <!--  -->
    <div class="ruleForm">
      <h1 class="title"></h1>
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="500px" class="demo-ruleForm">
       <!--  <el-form-item label="" prop="id" :label-width="formLabelWidth">
          <el-col :span="11" >
            <el-input v-model="ruleForm.id"></el-input>
          </el-col>
        </el-form-item> -->
        <el-form-item label="" prop="employName" :label-width="formLabelWidth">
          <el-col :span="11" >
            <el-input v-model="ruleForm.employName"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="sex" :label-width="formLabelWidth">
          <el-radio-group v-model="ruleForm.sex">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="employType" :label-width="formLabelWidth">
          <el-col :span="11">
            <el-select v-model="ruleForm.employType" placeholder="">
              <el-option label="" value="001"></el-option>
              <el-option label="" value="002"></el-option>
            </el-select>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="phoneNumber" :label-width="formLabelWidth">
          <el-col :span="11" >
            <el-input v-model="ruleForm.phoneNumber"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="address" :label-width="formLabelWidth">
          <el-col :span="11" >
            <el-input v-model="ruleForm.address"></el-input>
          </el-col>
        </el-form-item>
        <el-form-item label="" required :label-width="formLabelWidth">
          <el-col :span="11">
            <el-form-item prop="entrydate">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.entrydate" @change="getsTime" format="yyyy-MM-dd" style="width: 100%;"></el-date-picker>
              <!-- :picker-options="formatDate" -->
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="delivery" :label-width="formLabelWidth">
          <el-switch on-text="" off-text="" v-model="ruleForm.delivery"></el-switch>
        </el-form-item>

        <el-form-item label="" prop="birthday" :label-width="formLabelWidth">
          <el-date-picker v-model="ruleForm.birthday" type="date" placeholder="" ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="desc" :label-width="formLabelWidth">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
        <el-form-item class="btn">
          <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>
<script>
  import { Message } from 'element-ui';
  import {global} from '@/global/global';
  import {api} from '@/global/api';
  import {formatDate} from '@/filters/index';


  export default {
      data () {
          return {
            // msg: 'Welcome to Your Vue.js App'
            formLabelWidth:"120px",
            ruleForm: {
                  employName: '',//
                  sex: '',
                  employType: '',
                  phoneNumber:'',
                  address:'',
                  entrydate: '',
                  delivery: false,
                  birthday: '',
                  desc: ''
            },

            rules: {
              employName: [
                { required: true, message: '', trigger: 'blur' },
                { min: 2, max: 5, message: ' 3  5 ', trigger: 'blur' }
              ],
              // region: [
              //   { required: true, message: '', trigger: 'change' }
              // ],
              entrydate: [
                { type: 'date', required: true, message: '', trigger: 'change' }
              ]
              // date2: [
              //   { type: 'date', required: true, message: '', trigger: 'change' }
              // ],
              // type: [
              //   { type: 'array', required: true, message: '', trigger: 'change' }
              // ],
              // sex: [
              //   { required: true, message: '', trigger: 'change' }
              // ],
              // birthday: [
              //   { type: 'date', required: true, message: '', trigger: 'change' }
              // ],
              // desc: [
              //   { required: true, message: '', trigger: 'blur' }
              // ]
            },

            // pickerOptions0: {
            //          disabledDate(time) {
            //            return time.getTime() < Date.now() - 8.64e7;
            //          }
            //        }

          };
      },
      
      methods:{
          //id
          getFormData:function(){
            var vm = this;
            var data = this.$router.query;
            console.log('url',data);
          },
          submitForm(formName) {
               let that = this;
               // alert(1);
               this.$refs[formName].validate((valid) => {

                 if (valid) {

                   console.log('submit! ',this.ruleForm);
                   // console.log("",JSON.stringify(this.ruleForm));

                   global.post(api.addUser,this.ruleForm,null,function(res){
                        // alert(1);
                        console.log('',res)//
                        Message({
                            showClose:true,
                            message:'...',
                            type:'success'
                        })
                        setTimeout(()=>{
                            that.$router.push('./userInfo')
                        },2000)
                   },function(){})

                 } else {
                   console.log('error submit!!');
                   return false;
                 }
               });
          },
          resetForm(formName) {
              this.$refs[formName].resetFields();
          },
          //
          getsTime(val) {
                this.ruleForm.entrydate=val;//entrydatedatav-model
            }
       }




        }

      

</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  .new-user{
    position: absolute;
  }
  .new-user .title{
    text-align: center;
    margin-bottom: 50px;
  }
  .new-user .el-col-2{
    text-align: center;
  }
  .new-user .btn{
    margin-left: 100px;
  }

  
</style>


