<template>
  <div class="sale">
      <!--  -->
    <!--  <div class="bre-crumb">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>  
        </el-breadcrumb>
     </div> -->

     <!--   -->
     <div class="info-top">
       <el-form :inline="true" :model="searchOption" class="demo-form-inline">
           <el-form-item label="">
             <el-select v-model="searchOption.kinds"  placeholder="">
               <el-option label="A" value="a"></el-option>
               <el-option label="B" value="b"></el-option>
               <el-option label="C" value="c"></el-option>
               <el-option label="D" value="d"></el-option>
             </el-select>
           </el-form-item>
           <el-form-item>
           <el-form-item label="">
             <el-input v-model="searchOption.employeeName" placeholder="/"></el-input>
           </el-form-item>
           <el-button type="primary" v-waves icon="search" @click="onSubmit"></el-button>
         </el-form-item>
       </el-form>
     </div>

     <!--    -->
     <div class="datePicker inline-block">
        <span class="demonstration"></span>
        <el-date-picker v-model="value2" type="datetime" placeholder="" align="right"
         :picker-options="pickerOptions1"></el-date-picker>
      </div>

     <!--  -->
     <div class="info-con">
        <el-table :data="tableData" border show-summary style="width: 100%" :default-sort = "{prop: 'date', order: 'descending'}">
           <el-table-column prop="date" label="" sortable width="180"></el-table-column>
           <el-table-column prop="saleMan" label="" width="180"></el-table-column>
           <!-- <el-table-column prop="address" label="" :formatter="formatter"></el-table-column> -->
           <el-table-column prop="goods" label="" width="180"></el-table-column>
           <el-table-column prop="saleQuantity" label="/" sortable width=""></el-table-column>
           <el-table-column prop="saleAmount" label="/" sortable width=""></el-table-column>
        </el-table>
     </div>

     <div class="sale-more">
        <router-link to="./echarts"><img src="../.././assets/sale-more.jpg"><span></span></router-link>
     </div>

  </div>
</template>
<script>
  import {api} from '@/global/api'
  export default {
      data () {
          return {
            // 
            inputName: '',
            inputGood:'',
            searchOption:{
                kinds:'',
                employeeName:''
            },
            //
            tableData: null,
            //
            pickerOptions1: {
                   shortcuts: [{
                     text: '',
                     onClick(picker) {
                       picker.$emit('pick', new Date());
                     }
                   }, {
                     text: '',
                     onClick(picker) {
                       const date = new Date();
                       date.setTime(date.getTime() - 3600 * 1000 * 24);
                       picker.$emit('pick', date);
                     }
                   }, {
                     text: '',
                     onClick(picker) {
                       const date = new Date();
                       date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
                       picker.$emit('pick', date);
                     }
                   }]
            },
            value2: ''

          }
        },
        mounted:function(){
         var vm=this;
         vm.getData();//JSON
        },
        methods:{

          //json
           getData(){
            // return this.bb;

            this.$http.get(api.sale).then(function(response){
                // alert(1);
              console.log('response',response)
              // console.log("json",response.data.tableData)
              this.tableData = response.data.tableData;

            }, function(response){
              alert("")
            })
           },

          onSubmit(){
            console.log("");
          },
          //  
          // formatter(row, column) {
          //   return row.address;
          // }

         

        }
   }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  .sale{
    position: absolute;
  }
  .info-top{
    margin: 20px 100px;
  }
  .info-top .el-form--inline .el-form-item{
    margin-right: 50px;
  }
  .datePicker{
    text-align: right;
  }
  .info-con{
    margin: 20px 10px;
  }
  .sale-more{
    margin: 20px;
  }
  .sale-more img{
    width: 50px;
    height: 50px;

  }
  .sale-more span{
    display: block;
  }
</style>


