<!---->
<template>
  <div class="account-container">
    <!--  -->
    <div class="filter-container">
      <el-input style="width: 200px;" class="filter-item" placeholder="" v-model="listQuery.orderId">
      </el-input>

      <!-- <el-select clearable class="filter-item" style="width: 130px;" v-model="listQuery.type" placeholder="">
        <el-option v-for="item in  orderStatus" :key="item.value" :label="item.label" :value="item.value">
        </el-option>
      </el-select> -->

      <!--popover-->
      <el-popover
        ref="advancedSearchPopover"
        placement="top-start"
        width="1000"
        trigger="click"
        v-model="showAdvancedSearchPopover">
        <el-form label-width="80px" label-position="right">
          <el-row>
            <el-col :span="10">
              <el-form-item label="">
                <el-input size="small" placeholder="" v-model="listQuery.orderId" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="">
                <el-date-picker
                  type="datetimerange"
                  placeholder=""
                  clearable
                  align="right" v-model="listQuery.orderTime">
                </el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
      
          <el-row>
            <el-col :span="10">
              <el-form-item label="">
                <el-input size="small" placeholder="/" v-model="listQuery.customerInfo" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
        
            <el-col :span="10">
              <el-form-item label="">
                <el-input size="small" placeholder="/" v-model="listQuery.goodsInfo" style="width: 300px;"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
 
          <el-select clearable class="filter-item" style="width: 150px; margin-left:80px; margin-bottom: 20px;" v-model="listQuery.type" placeholder="">
            <el-option v-for="item in  orderStatus" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
      
          <el-form-item>
            <el-button type="primary" size="small" @click="handleAdvanceSearch"></el-button>
            <el-button size="small" @click="showAdvancedSearchPopover = false"></el-button>
          </el-form-item>
        </el-form>
      </el-popover>

      <el-button class="filter-item" type="primary" v-waves icon="search" @click="handleSearch" size="small"></el-button>
      <el-button class="filter-item" type="primary" v-waves icon="search" v-popover:advancedSearchPopover size="small" ></el-button>
      <el-button class="filter-item" type="primary" v-waves icon="document" size="small" @click="handleDownload"></el-button>    
    </div> 
    <!-- disabled="disabled"--> 
    <div class="top-distance">
      <el-row>
        <el-button class="filter-item" type="primary" v-waves @click="handleFilter" size="small" ></el-button>
        <el-button class="filter-item" type="primary" v-waves @click="handleWaitedPay" size="small" ></el-button>
        <el-button class="filter-item" type="primary" v-waves @click="handleWaitedSend" size="small" ></el-button>
        <el-button class="filter-item" type="primary" v-waves @click="handleSended" size="small" ></el-button>
        <el-button class="filter-item" type="primary" v-waves @click="handleSuccess" size="small"></el-button>
        <el-button class="filter-item" type="primary" v-waves @click="handleClose" size="small"></el-button>
        <el-button class="filter-item" type="primary" v-waves icon="edit" @click="handleSendAll" size="small" ></el-button>
        <el-button class="filter-item" type="primary" v-waves icon="edit" @click="handleDelAll" size="small" ></el-button>
      </el-row>
    </div>  
    <!--  -->
    <el-table ref="multipleTable" @selection-change="handleSelectionChange" :data="list" v-loading.body="listLoading" element-loading-text="" border fit highlight-current-row  >
         <el-table-column type="selection" width="55">
         </el-table-column>

          <el-table-column label="" align="center" width="110">
            <template scope="scope">
              {{scope.row.orderId}}
            </template>
          </el-table-column>

          <el-table-column align="center"  label="">
              <template scope="scope">
                <span>{{scope.row.goods}}</span>
              </template>
          </el-table-column>

          <el-table-column align="center"  label="">
              <template scope="scope">
                <span>{{scope.row.money}}</span>
              </template>
          </el-table-column>

          <el-table-column label="" align="center">
            <template scope="scope">
              <span>{{scope.row.realName}}</span>
            </template>
          </el-table-column>

          <el-table-column align="center" prop="created_at" label="" width="190">
            <template scope="scope">
              <span>{{scope.row.orderDay}}</span>
            </template>
          </el-table-column>

            <el-table-column align="center"  label="">
                <template scope="scope">
                  <span>{{scope.row.orderNum}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="">
                <template scope="scope">
                  <span>{{scope.row.collectionMoney}}</span>
                </template>
            </el-table-column>

            <el-table-column align="center"  label="">
                <template scope="scope">
                  <el-tag :type="scope.row.orderStatus | statusFilter">{{scope.row.orderStatus | statusFilterTip}}</el-tag>
                </template>
            </el-table-column>


            <el-table-column align="center"  label="" width="150">
                <template scope="scope">
                   <el-button class="edit" size="small" v-waves @click="handleEdit(scope.$index, scope.row)"></el-button>
                   <el-button class="delete" size="small" v-waves type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
                </template>
            </el-table-column>
    </el-table>
    <!--  -->
    <div v-show="!listLoading" class="pagination-container">
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page.sync="listQuery.currPage"
        :page-sizes="[10,20,30, 50]" :page-size="listQuery.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
  import { api } from '@/global/api'
  import { global } from '@/global/global'
  export default {
    data() {
      return {
        list: null,//list
        total: null,
        listLoading: false,
        showAdvancedSearchPopover: false,
        isIndeterminateStatus: false,
        checkAllStatus: true,
        checkedStatuss: [0, 1],
        listQuery: { //
          currPage: 1,
          pageSize: 10,
          orderId: '',
          orderTime: null,
          customerInfo: null,
          goodsInfo: null,
          type: null,     
        },
        orderStatus: [{label: '', value: 1}, {label: '', value: 2}, {label: '', value: 3}, {label: '', value: 4}, {label: '', value: 5}],
        dialogFormVisible: false,
        multipleSelection: []  //
      }
    },
    filters: {
        parseTime(time) {
          return parseTime(time);
        },
        statusFilter(status) {
            const statusMap = ['primary', 'success', 'warning', 'danger', 'gray'];
            return statusMap[status - 1];
        },
        statusFilterTip(status) {
            const statusMap = ['', '', '', '', ''];
            return statusMap[status - 1];
        }
    },
    mounted() {
      var vm = this;
      vm.getList();
    },
    methods: {
      initData (data) {
        var vm = this;
        vm.list = data;
        vm.listQuery.currPage = data.currPage;
        vm.listQuery.pageSize = data.pageSize;
        vm.total = data.total;
      },
      //, 
      getList () {
        var vm = this;
        //
        console.log("json",JSON.stringify(vm.listQuery));
        global.get( api.orderInfo, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data)  
          }, function(response) {
            alert("");
        }, false)
      },
      //handleWaitedPay handleWaitedSend handleSuccess handleClose
      //
      handleWaitedPay () {
        var vm = this;
        global.get( api.waitedPay, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data) 
          }, function(response) {
            alert("");
        }, false)
      },
      //
      handleWaitedSend () {
        var vm = this;
        global.get( api.waitedSend, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data)
          }, function(response) {
            alert("");
        }, false)
      },
      //
      handleSended () {
        var vm = this;
        global.get( api.sended, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data) 
          }, function(response) {
            alert("");
        }, false)
      },
      //
      handleSuccess () {
        var vm = this;
        global.get( api.success, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data) 
          }, function(response) {
            alert("");
        }, false)
      },
      //
      handleClose () {
        var vm = this;
        global.get( api.close, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data) 
          }, function(response) {
            alert("");
        }, false)
      },
      //
      handleEdit(index,row){
        var vm = this
        vm.handleSendAll()
      },
      //
      handleDelete(index,row){
        var vm = this;
        // vm.handleDelAll()
        //console.log('row',index,'-----',row);
        //
        console.log("index",index);
        console.log("row",row);
        vm.list.splice(index,1)
         // console.log(index, row);
      },
      //
      handleDelAll(){
        var vm = this;
        vm.$message({
          showClose: true,
          message: '',
          type: 'success'
        });
        //console.log('row',vm.multipleSelection);
      },
      //
      handleSendAll() {
        var vm = this;
        if(vm.multipleSelection.length == 0){
          // 
          vm.$message({
            showClose: true,
            message: '',
            type: 'success'
          });
          return
        }
        console.log('row',vm.multipleSelection);
        //
        this.$router.push('/index/order/editOrder');
      },
      //
      handleFilter() {
        this.showAdvancedSearchPopover = false;
        this.getList();
      },
      //
      handleSearch () {
        // var vm = this;
        // vm.$message({
        //   showClose: true,
        //   message: '' + vm.listQuery.orderId + ' ;  ' + vm.listQuery.type,
        //   type: 'success'
        // });

        //
        console.log("")
        var vm = this ;
        for(var i = 0;i < vm.list.length+1;i++){//
           for(var item of vm.list){//
              if(vm.listQuery.orderId != item.orderId){
                var index = vm.list.indexOf(item);
                console.log(index)
                vm.list.splice(index,1);//indexidindex
              }
            }
        }


      },
      //
      handleAdvanceSearch () {
        var vm = this;
        global.get( api.success, { params: vm.listQuery }, function(response) {
            var data = response.body;
            vm.initData (data.data.data) 
          }, function(response) {
            alert("");
        }, false)
        this.showAdvancedSearchPopover = false;
      },
      //
      handleSizeChange(val) {
        this.listQuery.pageSize = val;
        this.getList();
      },
      //
      handleCurrentChange(val) {
        this.listQuery.currPage = val;
        this.getList();
      },
      handleSelectionChange(val) {
          this.multipleSelection = val;
      },
      //
      handleDownload() {
        var vm = this;
        require.ensure([], () => {
          const { export_json_to_excel } = require('../../vendor/Export2Excel');
          const tHeader = ['', '', '', '', '', '', '', ''];
          const filterVal = ['orderId', 'goods', 'money', 'realName', 'orderDay', 'orderNum', 'collectionMoney', 'orderStatus'];
          const list = vm.list;
          const data = vm.formatJson(filterVal, list);
          export_json_to_excel(tHeader, data, 'excel');
        })
      },
      formatJson(filterVal, jsonData) {
        return jsonData.map(v => filterVal.map(j => v[j]))
      }
    }
  };
</script>
<style>
  .account-container {
    width: 1210px;
    padding-left: 20px;
    padding-top: 25px;
  } 
  @media only screen and (max-width: 1400px) {
    .account-container {
      width: 1160px;
      padding-top: 20px;
    } 
  }
  .account-container .el-table {
    margin-top: 20px;
  } 
  .account-container .edit {
    float: left;
  }
  .account-container .delete {
    float: right;
  }
  .account-container .pagination-container {
    margin-top: 15px;
  }
  .account-container .top-distance {
    margin-top: 15px;
  }
</style>


