import Vue from 'vue';
import { Loading } from 'element-ui';

export  const global = {
	/*
	 * getthis.$http.get(url, [options])
	 * @param url { String } - url
	 * @param options { Object } - options{params:{key:11}}
	 * @param sucCb { Function } - 
	 * @param errorCb { Function } - 
	 * @param isLoading { Boolean } - 
	 * @param isLogin { Boolean } - 
	 */
	get:function( url, options = {}, sucCb, errorCb, isLoading = true, isLogin ){
	    if(!url){
	        console.log('url');
	        return false ;
	    }
	    //laoding  2018.4.24
	    // var isLoading = isLoading===undefined ||(isLoading!==undefined && isLoading !== false )? true : false;

	    //
	    if(isLoading){
	        var loadingInstance = Loading.service({text:"..."});
	    }
	    Vue.http.get(url, options).then((response) => {
	        // 
	        setTimeout(function(){
	        	sucCb(response);
	        	if(isLoading){
	           		loadingInstance.close();
	        	}
	        },1000)
	        
	        
	    }, (response) => {
	        // 
	        errorCb(response);
	        if(isLoading){
	           loadingInstance.close();
	        }
	    })
	},

	 /**
         * postthis.$http.get(url, [options])
         * @param url { String } - url
         * @param body { Object } - bodynullbody{key:11}
         * @param options { Object } - optionsnull{params:{key:11}}
         * @param sucCb { Function } - 
         * @param errorCb { Function } - 
         * @param isLoading { Boolean } - 
         * @param isLogin { Boolean } - 
         */
        post:function( url,body,options,sucCb,errorCb,isLoading = true,isLogin ){
            if(!url){
                console.log('url');
                return false ;
            }
            //lss 2017-6-28 laoding
            // var isLoading = isLoading===undefined ||(isLoading!==undefined && isLoading !== false )? true : false;  

            //
            if(isLoading){
                var loadingInstance = Loading.service({text:"..."});
            }
            Vue.http.post(url,body,options).then((response) => {
                // 
                //console.log('')
                sucCb(response);
                if(isLoading){
                   loadingInstance.close();
                }
                 
            }, (response) => {
                // 
                //console.log('')
                errorCb(response);
                if(isLoading){
                   loadingInstance.close();
                }
            })
        },

        /**
         * author lss
         * ,, 2018-4-25 12:00:00
         * @objD 
         */
        formatDate: function (objD) {
            if (!objD) {
                return '';
            }

            objD = new Date(objD);
            var str;
            var yy = objD.getYear();
            if (yy < 1900) yy = yy + 1900;
            var MM = objD.getMonth() + 1;
            if (MM < 10) MM = '0' + MM;
            var dd = objD.getDate();
            if (dd < 10) dd = '0' + dd;
            var hh = objD.getHours();
            if (hh < 10) hh = '0' + hh;
            var mm = objD.getMinutes();
            if (mm < 10) mm = '0' + mm;
            var ss = objD.getSeconds();
            if (ss < 10) ss = '0' + ss;
            str = yy + "-" + MM + "-" + dd + " " + hh + ":" + mm + ":" + ss;
            return (str);
        },
        /**
         * author lss
         * ,, 2018-4-25
         * @objD 
         */
        formatDate2: function (objD) {
            if (!objD) {
                return '';
            }
            objD = new Date(objD);
            var str;
            var yy = objD.getYear();
            if (yy < 1900) yy = yy + 1900;
            var MM = objD.getMonth() + 1;
            //if(MM<10) MM = '0' + MM;
            var dd = objD.getDate();
            //if(dd<10) dd = '0' + dd;
            //0  
            str = yy + "-" + MM + "-" + dd;
            return (str);
        },
		
        
        /**
         * url
         */
        getUrlFn: function () {
            var querystr = window.location.href.split("?"),
                // var querystr = "http://xxxx.com/recharge.html?mid=&version=7701&from=music".split("?"),
                GETs = "",
                GET = "";
            if (querystr[1]) {
                GETs = querystr[1].split("&");
                GET = [];
                for (i = 0; i < GETs.length; i++) {
                    tmp_arr = GETs[i].split("=");
                    var key = tmp_arr[0];
                    GET[key] = tmp_arr[1];
                }
            }
            return GET;
            // return querystr[1];
        },
        /**
         * url dataaa.html?data={}
         */
        getUrlData: function () {
            var purl = window.location.href;
            purl = purl.substr(purl.indexOf("?") + 1);
            var urlData = JSON.parse(decodeURI(purl.substr(purl.indexOf("=") + 1)));

            return urlData;
        },
};


