<template>
	<!--  -->
    <div class="edit">
      <el-dialog title="" :visible.sync="dialogFormVisible1">
        <el-form :model="editForm">
          <!-- <el-form-item label="ID" :label-width="formLabelWidth">
              <el-input auto-complete="off">
                <template scope="scope">
                  {{scope.$index}}
                </template>
              </el-input>
          </el-form-item> -->
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.employName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-radio-group v-model="editForm.sex">
              <el-radio label=""></el-radio>
              <el-radio label=""></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
            <el-select v-model="editForm.employType" placeholder=""><!-- value -->
              <el-option label="" value="001"></el-option>
              <el-option label="" value="002"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.phoneNumber" auto-complete="off"></el-input>
          </el-form-item>
           <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.address" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" :label-width="formLabelWidth">
              <el-input v-model="editForm.entrydate" @change="getsTime" format="yyyy-MM-dd" auto-complete="off"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
         
            <el-button v-waves @click="handledoneEdit('ruleForm')"></el-button>
            <el-button v-waves type="primary" @click="cancelEdit('ruleForm')"></el-button>
          
        </div>
      </el-dialog>
      <router-view></router-view>
    </div>
</template>

<script>
	import { Message } from 'element-ui';
  import {global} from '@/global/global';
  import {api} from '@/global/api';

    export default {
    	data(){
    		return{
    			editForm: {
                 "employName": "",
                 "sex":"",
                 "employType": "",
                 "phoneNumber": "",
                 "address": "",
                 "entrydate": "",
          },
          dialogFormVisible1:true,//
          formLabelWidth: '120px',



    		}
    	},

    	mounted(){
    		var vm = this;

    		//()
            vm.getFormData();
    	},

    	methods:{
    		//id
    		getFormData:function(){
    			var vm = this;
    			var data = this.$route.query;
    			console.log('url',data);
    			//urlajaxuserInfo
          //get
    			global.get(api.queryUserItem,{params:{'id':data.id}},function(res){
    				console.log('-------id',JSON.stringify(res) );
    				if(res.body.resultCode == 0){
    					var res = res.body.data;
    					console.log('=====',res);
    					vm.editForm = res
    				}else{
    					//alert(res.body.resultMsg)
                            Message({
                                showClose: true,
                                message: res.body.resultMsg,
                                type: 'error'
                            });
    				}
    			},function(res){
    				//
    			},true)
    		},
    		//
    		handledoneEdit(formName){
    			let that = this;

          global.post('http://localhost:3000/api/modifyUser',this.editForm,null,function(res){
                //alert('',res)
                          
                          //
                          Message({
                              showClose: true,
                              message: '',
                              type: 'success'
                          })
                          setTimeout(()=>{
                            that.$router.push('./userInfo')
                          },2000)

              },function(res){
                alert('',res)
              })
    		},

    		//
    		cancelEdit(formName){
          var vm = this;
    			setTimeout(()=>{
                            vm.$router.push('./userInfo')
                          },500)
    		},
        //
        getsTime(val) {
              this.ruleForm.entrydate=val;//entrydatedatav-model
          }


    	}
    }

</script>

<style scoped>
/*	* {
	padding: 0;
	margin: 0;
	}*/
	
</style>


