<!---->
<template>
    <div class="createPost-container">
    	<el-form class="form-container" :model="postForm" :rules="rules" ref="postForm" label-width="80px"
             label-position="right">
	        <div style="float: right">
		        <el-row>
			        <el-button v-loading="loading" style="margin-left: 10px;" type="success" @click="submitForm()">
			        </el-button>
			        <el-button type="warning" @click="cancel()"></el-button>
		        </el-row>
	        	
	        </div>
      
        	<div class="createPost-main-container">
		        <!---->
		        <h3></h3>
		        <el-row>
		        	<el-col :span="12">
			            <el-form-item label="" prop="name">
			              <el-input v-model="postForm.name" size="small"></el-input>
			            </el-form-item>
		            </el-col>

		            <el-col :span="10" :push="1">
			            <el-form-item label="" prop="categoryId">
			              <el-cascader
			                v-model="postForm.categoryId"
			                expand-trigger="hover"
			                :options="lightList"
			                :show-all-levels="false"
			                size="small">
			              </el-cascader>
			            </el-form-item>
		            </el-col>
		        </el-row>
	  
		        <el-row>
		            <el-col :span="12">
			            <el-form-item label="" prop="unitId">
			                <el-select v-model="postForm.unitId" clearable placeholder="" size="small">
				                <el-option
				                  v-for="item in unitList"
				                  :key="item.value"
				                  :label="item.label"
				                  :value="item.value">
				                </el-option>
			                </el-select>
			            </el-form-item>
		            </el-col>
		        </el-row>
	  
		        <el-row>
		            <el-col :span="12">
			            <el-form-item label="" prop="tags">
			                <el-checkbox size="small" :indeterminate="isIndeterminateTag" v-model="checkAllTag"
			                           @change="handleCheckAllTagChange">
			                </el-checkbox>
			                <el-checkbox-group v-model="postForm.tags" @change="handleCheckedTagsChange" style="display: inline-block;margin-left: 15px;">
				                <el-checkbox size="small" v-for="tag in tags" :label="tag.id" :key="tag.id">{{tag.name}}
				                </el-checkbox>
			                </el-checkbox-group>
			            </el-form-item>
		            </el-col>
		        </el-row>

		        <!---->
		        <h3 class="toph3"></h3>
		        <div class="filter-container">
		        	<el-button size="small" type="success" icon="plus" @click="addSku"></el-button>
		        </div>
		        <el-table class="filter-container" :data="postForm.skus" border fit highlight-current-row style="width: 100%">
		            <el-table-column align="center" min-width="300px" label="">
			            <template scope="scope">
			              <el-input size="small" v-model="scope.row.name"></el-input>
			            </template>
		            </el-table-column>
		  
		            <el-table-column align="center" width="200px" label="">
			            <template scope="scope">
			            	<el-input-number :controls="false" size="small" v-model="scope.row.price" :min="1"></el-input-number>
			            </template>
		            </el-table-column>
		          
		            <el-table-column align="center" width="300px" label="">
			            <template scope="scope">
			            	<el-input-number size="small" v-model="scope.row.stock" :min="1"></el-input-number>
			                <el-checkbox size="small"></el-checkbox>
			            </template>
		            </el-table-column>
		    
		            <el-table-column align="center" label="" width="120">
			            <template scope="scope">
			                <el-button size="small" type="danger" icon="delete"></el-button>
			            </template>
		            </el-table-column>
		        </el-table>
	  
		        <!---->
		        <h3 class="toph3"><span style="font-size: 12px; color: darkgrey; padding-left: 10px;">20</span></h3>
		        <el-upload
		          action="https://jsonplaceholder.typicode.com/posts/"
		          list-type="picture-card"
		          :on-preview="handlePictureCardPreview"
		          :on-remove="handlePictureRemove"
		          :before-upload="beforeAvatarUpload"
		          :on-success="handlePictureSuccess"
		          multiple>
		          <i class="el-icon-plus"></i>
		        </el-upload>
	  
		        <el-dialog v-model="showImagePreview" size="tiny">
		        	<img width="100%" :src="previewImageUrl" alt="">
		        </el-dialog>
		  
       		</div>
    	</el-form>
  </div>
</template>

<script>
    import sortable from 'html5sortable';
    // import wangeditor from 'wangeditor';//
  
    export default {
	    name: 'addProduct',
	    data() {
	        return {
		        editor: undefined,
		        postForm: {
		          name: '',
		          categoryId: undefined,
		          unitId: undefined,
		          tags: [],
		          skus: []
		        },
		        rules: {
		            name: [
		            	{required: true, message: '', trigger: 'blur'}
		          ],
		            categoryId: [
		            	{required: true, message: '', trigger: 'blur'}
		          ],
		            unitId: [
		            	{required: true, message: '', trigger: 'blur'}
		          ],
		            skus: [
		            	{required: true, message: '', trigger: 'blur'}
		          ]
		        },
		        loading: false,
		        lightList: [
		        {
		            value: 1,
		            label: '',
		            children: [
			            {
				            value: 4,
				            label: '',
				            children: [
					            { value: 10, label: '' }, 
					            { value: 11, label: '' }
				            ]
			            },
				        {
				            value: 5,
				            label: '',
				            children: [
					            { value: 12, label: '' }, 
					            { value: 13, label: ''},
					            { value: 14, label: ''}
				            ]
				        },
		            ]
		        }, 
		        {
		            value: 2,
		            label: '',
		            children: [
		            	{   value: 6, 
		            		label: '',
		            		children: [
					            { value: 15, label: '' }, 
					            { value: 16, label: ''},
					            { value: 17, label: ''}
				            ]
		            	}, 
		            	{   value: 7, 
		            		label: '',
		            		children: [
					            { value: 18, label: '' }, 
					            { value: 19, label: ''},
					            { value: 20, label: ''},
					            { value: 21, label: ''}
				            ]
		            	}
		            ]
		        }, 
		        {
		            value: 3,
		            label: '',
		            children: [
			            { value: 8, label: '' }, 
			            { value: 9, label: ''}
		            ]
		        }],
		        unitList: [{label: '', value: 1}, {label: '', value: 0}],
		        tags: [{id: 1, name: ''}, {id: 2, name: ''}, {id: 3, name: ''}],
		        checkAllTag: true,
		        isIndeterminateTag: true,
		        showImagePreview: false,
		        previewImageUrl: ''
	        };
	    },
	    computed: {},
	    created() {
	    },
	    mounted() {
	    	//
	        // this.editor = new wangeditor('#editor');
	        // this.editor.create();
	    },
	    filters: {},
	    methods: {
	        submitForm() {
	        	// 
	        	this.$router.go(-1);
	        },
	        cancel() {
	        	this.$router.go(-1);
	        },
	        //
	        handleCheckAllTagChange(event) {
		        let tags = [];
		        for (let tag of this.tags) {
	           		tags.push(tag.id);
	        	}
		        this.postForm.tags = event.target.checked ? tags : [];
		        this.isIndeterminateTag = false;
	        },
	        //
	        handleCheckedTagsChange(value) {
		        let checkedCount = value.length;
		        this.checkAllTag = checkedCount === this.tags.length;
		        this.isIndeterminateTag = checkedCount > 0 && checkedCount < this.tags.length;
	        },
	        //
	        addSku() {
	        	this.postForm.skus.push({name: '', stock: 1, price: 1});
	        },
	        //
	        handlePictureRemove(file, fileList) {
	        	console.log("===", file, fileList);
	        },
	        handlePictureCardPreview(file) {
		        this.previewImageUrl = file.url;
		        this.showImagePreview = true;
	        },
	        beforeAvatarUpload(file) {
	            // const isJPG = file.type === 'image/jpeg';
		        const isLt2M = file.size / 1024 / 1024 < 2;

		       // if (!isJPG) {
		       //   this.$message.error(' JPG !');
		       // }
		        if (!isLt2M) {
		          this.$message.error(' 2MB!');
		        }
		        return isLt2M;
	        },
	        handlePictureSuccess(response, file, fileList) {
		        this.$nextTick(() => {
		            sortable('.el-upload-list--picture-card', {
			            items: '.el-upload-list__item',
			            forcePlaceholderSize: true,
			            placeholderClass: 'my-sortable-placeholder'
		            });
		        });
	        }
	    }
    };
</script>

<style>
    .createPost-container {
	    position: relative;
	    padding-left: 40px;
	    padding-right: 40px;
	}
    .createPost-main-container>h3 {
		padding-bottom: 20px;
    }
    .filter-container {
    	margin-bottom: 15px;
    }
    .toph3 {
    	margin-top: 20px;
    }
</style>




