<script>

  import {api} from '@/global/api'
  import Vue from 'vue'
  import {export_json_to_excel} from '@/vendor/Export2Excel';//
  
  export default {
      data () {
          return {
            activeName2: 'first',
            IntableList: null,
            OuttableList:null,
            //
            inTemp: {
              "number":"" ,
              "ID": 22,
              "spbh":"" ,
              "name":"",
              "dw":"",
              "ck":"",
              "quantity":"",
              "rkdj":"",
              "rkje":"",
              "bz":""
            }, 
            outTemp: {
              "number":"" ,
              "PaymentID": "00",
              "AccountName":"" ,
              "ways":"",
              "money":"",
              "note":""
            },         
            IndialogCreatVisible: false,//
            IndialogEditVisible: false,

            dialogCreatVisible: false,//
            dialogEditVisible: false,
            dialogDeleteVisible:false,
          }
      },
      mounted() {
        var vm = this;
        vm.getData1();//
        vm.getData();//
      },
      methods:{
       // --------------------------
        getData1 () {
         this.$http.get(api.kfData).then(function (response) {
           console.log(response)
           console.log('json', response.data.IntableData)
           this.IntableList = response.data.IntableData;
         }, function (response) {
           alert('')
         })
        },
       //
        InhandleCreate() {      
          this.IndialogCreatVisible = true;
        },
        //
        InhandleEdit(){
          this.IndialogEditVisible = true;
        },
        //
        InhandleCreateSubmit(){
          var vm = this;         
          var flag=0;   
          for(var intablelist of vm.IntableList){
           console.log('flag',flag)
            if(vm.inTemp.spbh!=intablelist.spbh){
                console.log('',intablelist.spbh)
                flag++;
              }
            }
            console.log("ifflag",flag)
            if(flag==vm.IntableList.length){
            vm.IntableList.push(vm.inTemp);
            vm.inTemp=JSON.parse(JSON.stringify(vm.inTemp));
            }
            else{
              alert(',');
            }        
          this.IndialogCreatVisible = false;
        },
        //
        InhandleEditSubmit(){
          var vm = this;          
          var flag=0;
          for(var intablelist of vm.IntableList){
            if(vm.inTemp.spbh==intablelist.spbh){
              var index=vm.IntableList.indexOf(intablelist)
              console.log("",index);
              Vue.set(vm.IntableList,index,vm.inTemp);//
              vm.inTemp=JSON.parse(JSON.stringify(vm.inTemp));
              flag++;    
            }
          }
          if (flag==0) {alert("")};
          this.IndialogEditVisible = false;
        },
        // ----------------------------
        getData () {
          this.$http.get(api.kfData, {params: {'key': this.inputValue}}).then(function (response) {
            console.log(response)
            console.log('json', response.data.OuttableData)
            this.OuttableList = response.data.OuttableData;
          }, function (response) {
            alert('')
          })
        },
        handleCreate() {      
          this.dialogCreatVisible = true;
        },
        handleEdit(){
        this.dialogEditVisible = true;
        },
        handleDelete(){
        this.dialogDeleteVisible = true;
        },
        //
        handleCreateSubmit(){
          var vm = this;         
           var flag=0;   
           for(var outtablelist of vm.OuttableList){
           console.log('flag',flag)
            if(vm.outTemp.PaymentID!=outtablelist.PaymentID){
                console.log('PaymentID',outtablelist.PaymentID)
                flag++;
              }
            }
            console.log("ifflag",flag)
            if(flag==vm.OuttableList.length){
            vm.OuttableList.push(vm.outTemp);
            vm.outTemp=JSON.parse(JSON.stringify(vm.outTemp));
            }
            else{
              alert('ID,');
            }        
          this.dialogCreatVisible = false;
        },
         //
        handleEditSubmit(){
          var vm = this;          
          var flag=0;
          for(var outtablelist of vm.OuttableList){
            if(vm.outTemp.PaymentID==outtablelist.PaymentID){
              var index=vm.OuttableList.indexOf(outtablelist)
              console.log("",index);
              Vue.set(vm.OuttableList,index,vm.outTemp);
              vm.outTemp=JSON.parse(JSON.stringify(vm.outTemp));
              flag++;    
            }
          }
          if (flag==0) {alert("")};
          this.dialogEditVisible = false;
        },
        //
        handleDeleteSubmit(){
          var vm = this;          var flag=0;
          for(var outtablelist of vm.OuttableList){
          if(vm.outTemp.PaymentID==outtablelist.PaymentID){
            var index=vm.OuttableList.indexOf(outtablelist)
              console.log("",index);
              vm.OuttableList.splice(index,1)
              vm.outTemp=JSON.parse(JSON.stringify(vm.outTemp));
              flag++;
          }         
        } 
        if (flag==0) {alert("")};
        this.dialogDeleteVisible = false;
        },
        //
        handleDownload(){
           // console.log("");
           let vm = this;

           require.ensure([], () => {
             // const { export_json_to_excel } = require('vendor/Export2Excel');
             const tHeader = ['', '', '', '', '()','()',''];
             const filterVal = ['spbh', 'name', 'dw', 'quantity', 'rkdj','rkje','bz'];
             const IntableList = vm.IntableList;
             const data = vm.formatJson(filterVal, IntableList);
             export_json_to_excel(tHeader, data, 'excel');
           })
        },
        formatJson(filterVal, jsonData) {
          return jsonData.map(v => filterVal.map(j => v[j]))
        },
      }
  }
</script>

<style>
  * {
    padding: 0;
    margin: 0;
  }
  a {
    text-decoration: none;
  }
  .post1{
    margin:20px;
    position: absolute;
  }
  .post1 .el-tabs{
    margin: 20px;
  }
  .post1 .el-tab-pane{
    /*border: 1px solid;*/
    /*width: 900px;*/
  }
  .el-dialog--small {
      width: 41%;
  }
  .post1 .el-form-item{
    float: left;
    width: 400px;
  }
  .post1 .el-form-item label{
    width: 100px;
    text-align: left;
  }
  .post1 .el-form-item .el-select{
    width: 150px;
  }
  .post1 .el-form-item .el-input{
    width: 300px;
  }
  .post1 .divider{
    width: 900px !important;
    background: #20A0FF;
    color: #fff;
    clear: both;
  }
  .post1 .el-rate{
    padding-top: 7px;
  }
  .post1 .clear{
    clear: both;
    background: red;
  }
  .post1 .span2{
    padding-right: 20px;
  }
  .post1 .pane2 li{
    margin-bottom: 5px;
    height: 50px;
  }
  .post1 .span3{
    margin: 50px 0px 20px 0px;
    width: 900px !important;
    height: 30px;
    background: gray;
    color: #fff;
  }
  .post1 .el-carousel__item h3 {
      color: #475669;
      font-size: 14px;
      opacity: 0.75;
      line-height: 200px;
      margin: 0;
    }
    
    .post1 .el-carousel__item:nth-child(2n) {
      background-color: #99a9bf;
    }
    
    .post1 .el-carousel__item:nth-child(2n+1) {
      background-color: #d3dce6;
    }
    /**/
    .inbox1 .el-dialog__body{
      position: relative;
      overflow: hidden;
    }
    .inbox2 .el-dialog__body{
      position: relative;
      overflow: hidden;
    }
</style>





